/*! General layout grid rules */

@use '../abstracts' as *;
@use 'sass:math';
@use 'sass:map';
@use 'sass:list';

$grid-columns: 12;

// Will be doubled as the sum of the padding of neighboring elements
$grid-gutter-width: math.div(rem($grid-gutter-default), 2);
$gutters: (
  0: 0,
  1: $grid-gutter-width * 0.25,
  2: $grid-gutter-width * 0.5,
  3: $grid-gutter-width,
  4: $grid-gutter-width * 1.5,
  5: $grid-gutter-width * 3,
);

@mixin row-columns-generator($grid-columns, $viewport: null) {
  @if $viewport {
    $viewport: '-' + $viewport;
  }
  @for $i from 1 through $grid-columns {
    .row-cols#{$viewport}-#{$i} > * {
      flex: 0 0 auto;
      width: math.div(100%, $i);
    }
  }
}

@mixin column-generator($grid-columns, $viewport: null) {
  @if $viewport {
    $viewport: '-' + $viewport;
  }
  @for $i from 1 through $grid-columns {
    .col#{$viewport}-#{$i} {
      width: math.div($i * 100%, $grid-columns);
    }
  }
}

@mixin gutter-generator($gutters, $viewport: null) {
  $gutter-types: (g, gx, gy);
  @if $viewport {
    $viewport: '-' + $viewport;
  }
  @for $i from 1 through list.length($gutter-types) {
    $type: list.nth($gutter-types, $i);
    @each $key, $value in $gutters {
      .#{$type}#{$viewport}-#{$key} {
        @if $type == g {
          margin-top: -$value * 2;
          margin-right: -$value;
          margin-left: -$value;
        }
        @if $type == gx {
          margin-right: -$value;
          margin-left: -$value;
        }
        @if $type == gy {
          margin-top: -$value * 2;
        }
      }
      .#{$type}#{$viewport}-#{$key} > * {
        @if $type == g {
          padding-right: $value;
          padding-left: $value;
          margin-top: $value * 2;
        }
        @if $type == gx {
          padding-right: $value;
          padding-left: $value;
        }
        @if $type == gy {
          margin-top: $value * 2;
        }
      }
    }
  }
}

.container {
  width: 100%;
  padding-right: 1.5rem;
  padding-left: 1.5rem;
  margin: 0 auto;
  @if ($responsive-layout == true) {
    min-width: rem($container-min-width);
    max-width: rem(map.get($container-max-widths, $max-responsive-viewport));
  }
  @include bp(md) {
    padding-right: 2rem;
    padding-left: 2rem;
  }
}

// Viewport width container
.vw-container {
  position: relative;
  width: 100vw;
  margin-left: calc(-50vw + 50%);
  // Offset right
  &--right {
    margin-left: initial;
  }
  // Offset left
  &--left {
    width: initial;
  }

  // Convert into normal container for the smallest viewport
  @include bp-max(map.get($container-max-widths, index-to-key(1, $container-max-widths))) {
    min-width: rem($container-min-width);
    width: 100%;
    margin-left: initial;
  }
}

@if $grid {
  /*! Bootstrap-like custom grid system */

  .row {
    display: flex;
    flex-flow: row wrap;
  }

  .col {
    flex: 1 0 0;
  }

  // Row columns: .row-cols-1, .row-cols-2 etc...
  @include row-columns-generator($grid-columns);

  .col-auto,
  [class*='col-'] {
    flex: 0 0 auto;
  }

  // Columns: .col-1, .col-2 etc...
  @include column-generator($grid-columns);

  // Gutters: .g-0, .gx-1, gy-2 etc...
  @include gutter-generator($gutters);

  // Containers and responsive classes:
  // .container, .col-xs-1, .g-xs-2 etc...
  @each $viewport, $size in $viewports {
    @include bp($viewport) {
      .container {
        @if (list.index(map.keys($container-max-widths), $viewport) == 1) {
          min-width: rem($container-min-width);
        }
        max-width: rem(map.get($container-max-widths, $viewport));
      }
      @include row-columns-generator($grid-columns, $viewport);
      @include column-generator($grid-columns, $viewport);
      @include gutter-generator($gutters, $viewport);
    }
  }
}
