/*! Dropdown lists */

@use '../abstracts' as *;

.dropdown {
  position: relative;
  display: flex;
}

.dropdown__button {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  width: 2rem;
  height: 2rem;
  border: none;
  background-color: transparent;
  color: var(--color-primary);
  transition: color var(--transition-medium-cubic);
  &:hover,
  &:focus-visible {
    color: var(--color-secondary);
  }
  &:active {
    color: var(--color-accent);
  }

  .icon {
    font-size: 1.8rem;
  }
}

.dropdown-list {
  position: absolute;
  top: calc(100% + 1.4rem);
  left: 0;
  z-index: 3;
  display: none;
  flex-direction: column;
  gap: 1rem;
  padding: 1rem 0;
  border-top: 1px solid var(--color-secondary);
  border-radius: 0 0 1.5rem 1.5rem;
  margin: 0;
  list-style: none;
  background-color: var(--color-white);
  box-shadow: var(--shadow-menu);
  transform: translateX(calc(-50% + 1rem));
  animation: sublist-appear 0.2s cubic-bezier(0.33, 1, 0.68, 1);

  &.is-visible {
    display: flex;
  }
}

.dropdown-item {
  min-width: 16rem;
  width: max-content;
  max-width: 26rem;
  padding: 0 2rem 1rem;
  border-bottom: 1px solid var(--color-primary-05);
  margin-bottom: 0;
  line-height: 1.6rem;
  &:last-child {
    padding-bottom: 0;
    border: none;
  }
}
