/* Custom select component */

@use '../abstracts/' as *;

.custom-select-container {
  flex-grow: 1;
  border: 1px solid var(--color-primary-20);
  border-radius: 1rem;
  font-size: 1.4rem;
  user-select: none;

  select {
    width: 1rem;
  }
}

.custom-select-opener {
  position: relative;
  z-index: 2;
  display: flex;
  overflow: hidden;
  align-items: center;
  width: 100%;
  height: 4.6rem;
  padding-left: 1.9rem;
  // border: 1px solid var(--color-gray-5);
  border-radius: 4rem;
}

.custom-select-opener[aria-expanded='true'] {
  z-index: 4;
}

.custom-select-panel {
  top: calc(100% + 0.1rem);
  left: 0;
  z-index: 1;
  display: none;
  padding: 1rem 2rem 2rem;
  border-radius: 0 0 1.5rem 1.5rem;
  background-color: var(--color-white);
  box-shadow: var(--shadow-menu);
}

.select-wrapper .custom-select-panel {
  left: -0.1rem;
  width: calc(100% + 0.2rem);
}

.custom-select-container.is-open {
  .custom-select-panel {
    z-index: 3;
    display: block;
    max-height: 23rem;
    animation: hide-scroll 0.15s backwards;
    animation: sublist-appear 0.2s cubic-bezier(0.33, 1, 0.68, 1);
  }

  & + .select-wrapper__chevron {
    color: var(--color-secondary);
    transform: rotate(-180deg);
  }
}

@keyframes hide-scroll {
  from,
  to {
    overflow: hidden;
  }
}

.custom-select-option {
  padding: 1rem 0 0.9rem;
  border-bottom: 1px solid var(--color-primary-05);
  font-size: 1.4rem;
  line-height: 1.8rem;

  &.is-selected {
    font-weight: 400;
  }

  &.has-focus {
    background-color: transparent;
    color: var(--color-secondary);
  }
}

.custom-select-option[data-value=''] {
  display: none;
}

.header__controls {
  .custom-select-container {
    display: flex;
    border: none;
    font-family: var(--font-bold);
    font-size: 1.3rem;
    &:hover,
    &:focus-visible {
      & ~ .icon {
        color: var(--color-secondary);
      }
    }
    &:active {
      & ~ .icon {
        color: var(--color-accent);
      }
    }
    &.is-open {
      & ~ .icon {
        color: var(--color-secondary);
      }
      & ~ .header-select-wrapper__chevron {
        transform: rotate(-180deg);
      }
    }
  }
  .custom-select-opener {
    height: 2rem;
    padding-right: 1.4rem;
    padding-left: 2.4rem;
    @include bp(lg) {
      padding-right: 1.8rem;
      padding-left: 2.8rem;
    }
  }
  .custom-select-panel {
    top: calc(100% + 1.4rem);
    left: calc(-14rem + (7rem + 50%));
    width: initial;
    padding: 1rem 0;
    border-top: 1px solid var(--color-secondary);
    @include bp-max(359.98px) {
      left: -80%;
    }
  }
  .custom-select-option {
    min-width: 14rem;
    width: max-content;
    max-width: 26rem;
    padding: 0 2rem 1rem;
    border-bottom: 1px solid var(--color-primary-05);
    margin-bottom: 1rem;
    font-size: 1.3rem;
    line-height: 1.6rem;
    text-align: center;
    transition: color var(--transition-medium-cubic);
    &:hover,
    &:focus-visible {
      color: var(--color-secondary);
    }
    &:active {
      color: var(--color-accent);
    }
    &:last-child {
      padding-bottom: 0;
      border-bottom: none;
      margin-bottom: 0;
    }
  }
}

.header-select-wrapper {
  position: relative;
  display: flex;

  &__icon {
    position: absolute;
    transition: color var(--transition-medium-cubic);
  }

  &__chevron {
    position: absolute;
    top: 0.5rem;
    right: 0;
    font-size: 1rem;
    transition: all var(--transition-medium-cubic);
  }
}
