/*! Various card & panel views */

@use '../abstracts/' as *;

.card {
  border-radius: 2rem;
  background-color: var(--color-white);
  transition: box-shadow var(--transition-medium-cubic);
  &:hover {
    box-shadow: var(--shadow-card);
  }
}

.panel {
  width: 100%;
  padding: 2rem;
  border-radius: 2rem;
  background-color: var(--color-white);
  @include bp(xl) {
    padding: 3rem;
  }

  .h2 {
    padding-bottom: 1rem;
    font-family: var(--font-bold);
    font-size: 2rem;
    line-height: 2.5rem;
  }
}

/* Product cards */

.product-card {
  display: flex;
  flex-direction: column;
  padding: 0 1rem 1rem;
  @include bp(md) {
    gap: 1rem;
    padding: 2rem;
  }
  @include bp(xl) {
    padding: 3rem;
  }
}

.product-card__img {
  &:hover,
  &:focus-visible {
    & + .product-card__heading .product-card__brand {
      color: var(--color-secondary);
    }
  }
  &:active {
    color: var(--color-accent);
  }

  img {
    width: 14rem;
    height: 14rem;
  }
}

.product-card__heading {
  margin-bottom: 0;
  @include bp-max(359.98px) {
    & + .product-card__controls {
      margin-top: 1rem;
    }
  }
}

.product-card__brand {
  font-family: var(--font-bold);
  font-size: 1.8rem;
  line-height: 2.3rem;
  text-decoration: none;
  color: var(--color-primary);
  @include bp(md) {
    font-size: 2.4rem;
    line-height: 3rem;
  }
}

.product-card__ingredient {
  width: fit-content;
  text-decoration: none;
  @include bp(sm) {
    font-size: 1.2rem;
    line-height: 1.5rem;
  }
  @include bp(md) {
    margin-top: -1.2rem;
  }

  &:hover,
  &:focus-visible {
    color: var(--color-primary);
  }
  &:active {
    color: var(--color-secondary);
  }
}

.product-card__text {
  font-size: 1.3rem;
  @include bp(sm) {
    margin: 1rem 0;
    font-size: 1rem;
    line-height: 1.6rem;
  }
}

.product-card__controls {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: auto;
}

.product-card__button {
  @include bp(sm) {
    padding: 0.5rem 1.4rem;
    .button__text {
      display: none;
    }
  }
}

.product-card__price {
  font-family: var(--font-bold);
  font-size: 1.6rem;
  line-height: 2rem;
  color: var(--color-secondary);
  @include bp(md) {
    font-size: 1.8rem;
    line-height: 2.3rem;
  }
}

/* Combo cards */

.combo-cards {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  @include bp-max(359.98px) {
    display: flex;
    flex-direction: column;
  }
  @include bp-max(574.98px) {
    grid-column: span 2;
    grid-row: 2;
    flex-direction: row;
    gap: 1rem;
  }
}

.combo-card {
  flex: 1 0 0;
  background-image: url('~SVG/ui/happy-day.svg');
  background-position: center right 3rem;
  background-repeat: no-repeat;
  @include bp(xs) {
    background-position: center right 1.4rem;
  }
  @include bp(sm) {
    background-position: center right 2.1rem;
    background-size: 4.4rem;
  }

  &--sale {
    background-image: url('~SVG/ui/sale.svg');
    background-position: calc(100% - 3.1rem) calc(50% - 0.1rem);
    @include bp(sm) {
      background-position: calc(100% - 2.1rem) 50%;
    }
    .combo-card__title {
      line-height: 2.4rem;
      @include bp(sm) {
        margin-top: -0.5rem;
      }
    }
  }
}

.combo-card__link-wrapper {
  width: 100%;
  height: 100%;
  padding: 2rem 6rem 1.4rem 2rem;
  @include bp-min(575px) {
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 1rem;
  }
  @include bp(md) {
    justify-content: initial;
    gap: 0;
  }
  @include bp(xl) {
    padding: 3rem 10.4rem 3rem 3rem;
  }
}

.combo-card__title {
  padding-right: 2rem;
  margin-bottom: 0.2rem;
  font-family: var(--font-bold);
  font-size: 1.8rem;
  line-height: 2.4rem;
  @include bp(md) {
    padding-right: 0;
    margin: 2.4rem 0 0.9rem;
    font-size: 2.4rem;
    line-height: 3rem;
  }
}

.combo-card__text {
  line-height: 1.8rem;
  @include bp(sm) {
    font-size: 1.2rem;
    line-height: 1.5rem;
  }
}

/* Testimonials */

.testimonial {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
  padding: 3rem;
}

.testimonial__header {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}

.testimonial__author {
  font-family: var(--font-bold);
  font-size: 1.8rem;
  line-height: 2.3rem;
}

.testimonial__text {
  font-size: 1.3rem;
}

/* Rating */

.rating {
  display: flex;
  gap: 0.2rem;
}

.rating__star {
  width: 1.8rem;
  height: 1.8rem;
  background-image: url('~SVG/ui/star.svg');
  background-position: center;
  background-repeat: no-repeat;
}

/* Product info panels */

.info-panel {
  display: flex;
  flex-direction: column;
  gap: 1rem;

  a {
    display: inline;
    &:hover,
    &:focus-visible {
      color: var(--color-primary);
    }
    &:active {
      color: var(--color-secondary);
    }
  }
}

.info-panel__image {
  width: 20rem;
  height: 20rem;
}

.info-panel--gift .info-panel__image {
  width: initial;
  height: 13.7rem;
  margin: 0 auto;
}

/* Cart info panels */

.cart-panel {
  display: flex;
  flex-wrap: wrap;
  gap: 3rem;
  @include bp(md) {
    gap: 2rem;
  }
  @include bp-min(1240px) {
    flex-direction: column;
    gap: 0;
  }
}

.cart-panel-item {
  display: flex;
  flex-basis: 100%;
  flex-direction: column;
  justify-content: center;
  gap: 0.5rem;
  @include bp(md) {
    flex-basis: calc(50% - 1rem);
  }
  @include bp-min(1240px) {
    flex-basis: initial;
    padding-bottom: 2rem;
    border-bottom: 1px solid var(--color-primary-05);
    margin-bottom: 2rem;
  }
  &--2 .cart-panel-item__title {
    background-image: url('~SVG/ui/box.svg');
  }
  &--3 .cart-panel-item__title {
    background-image: url('~SVG/ui/dashboard.svg');
  }
  &--4 .cart-panel-item__title {
    background-image: url('~SVG/ui/shield.svg');
  }
  &--5 .cart-panel-item__title {
    background-image: url('~SVG/ui/wallet.svg');
  }
  &:last-child {
    padding-bottom: 0;
    border: none;
    margin-bottom: 0;
  }
}

.cart-panel-item__title {
  min-height: 2.2rem;
  padding-left: 3.2rem;
  font-family: var(--font-bold);
  font-size: 1.4rem;
  line-height: 1.8rem;
  background-image: url('~SVG/ui/discount.svg');
  background-position: center left;
  background-repeat: no-repeat;
}

.cart-panel-item__text {
  font-size: 1.3rem;
  line-height: 1.6rem;
}
