@charset "UTF-8";
/*! Typography settings */
@font-face {
  font-family: "Plus Jakarta Sans Regular";
  src: url("~Fonts/plus-jakarta-sans-regular.woff2") format("woff2"), url("~Fonts/plus-jakarta-sans-regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: fallback;
}
@font-face {
  font-family: "Plus Jakarta Sans Medium";
  src: url("~Fonts/plus-jakarta-sans-medium.woff2") format("woff2"), url("~Fonts/plus-jakarta-sans-medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: fallback;
}
@font-face {
  font-family: "Plus Jakarta Sans Bold";
  src: url("~Fonts/plus-jakarta-sans-bold.woff2") format("woff2"), url("~Fonts/plus-jakarta-sans-bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: fallback;
}
:root {
  --font-regular: "Plus Jakarta Sans Regular", sans-serif;
  --font-medium: "Plus Jakarta Sans Medium", sans-serif;
  --font-bold: "Plus Jakarta Sans Bold", sans-serif;
}

.critical-css-font-carrier {
  font-family: "Plus Jakarta Sans Regular";
  font-family: "Plus Jakarta Sans Medium";
  font-family: "Plus Jakarta Sans Bold";
}

/*! Initial styling for basic html-elements */
*,
::before,
::after {
  box-sizing: border-box;
}

* {
  padding: 0;
  margin: 0;
}
*:focus {
  outline: none;
}
*:not(input, textarea):focus-visible {
  outline: 1px dotted var(--tab-focus-color);
}

html {
  height: 100%;
  font-family: system-ui, "-apple-system", "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji";
  font-size: 10px;
  background-color: var(--html-bg-color);
  scroll-behavior: smooth;
  -webkit-text-size-adjust: 100%;
}

body {
  overflow-x: hidden;
  height: 100%;
  font-family: var(--font-regular);
  font-size: 1.4rem;
  line-height: 2rem;
  color: var(--text-body-color);
  -webkit-font-smoothing: antialiased;
}
@media (max-width: 320px) {
  body {
    overflow-x: auto;
  }
}
body.no-transition * {
  transition: none !important;
}
body.no-transition *::before, body.no-transition *::after {
  transition: none !important;
}

p,
h1,
h2,
h3,
h4,
h5,
h6 {
  overflow-wrap: break-word;
}

h1,
.h1,
h2,
.h2,
h3,
.h3 {
  font-family: var(--font-bold);
  font-weight: 700;
}

h1,
.h1 {
  margin-bottom: 1rem;
  font-size: 3rem;
  line-height: 3.8rem;
}
@media (min-width: 700px) {
  h1,
  .h1 {
    margin-bottom: 2rem;
    font-size: 4rem;
    line-height: 5rem;
  }
}

h2,
.h2 {
  margin-bottom: 1rem;
  font-size: 1.8rem;
  line-height: 2.2rem;
}
@media (min-width: 700px) {
  h2,
  .h2 {
    font-size: 2.2rem;
    line-height: 2.8rem;
  }
}

h3,
.h3 {
  font-size: 1.6rem;
  line-height: 2rem;
}

p {
  margin-bottom: 1.6rem;
}

@media (min-width: 700px) {
  br.sm {
    display: none;
  }
}

@media (max-width: 699.98px) {
  br.md {
    display: none;
  }
}

br.md-only {
  display: none;
}
@media (min-width: 700px) and (max-width: 919.98px) {
  br.md-only {
    display: initial;
  }
}

br.lg {
  display: none;
}
@media (min-width: 920px) {
  br.lg {
    display: initial;
  }
}

ul,
ol {
  padding-left: 2rem;
  margin-bottom: 1.6rem;
  list-style-position: inside;
}

li {
  margin-bottom: 1rem;
}
li > * {
  vertical-align: top;
}

a,
.link-button {
  display: inline-block;
  border: none;
  text-decoration: underline;
  background-color: transparent;
  color: var(--link-color);
  transition-property: transform, opacity, var(--transition-props-color);
  transition-timing-function: var(--transition-func-cubic);
  transition-duration: var(--transition-duration-medium);
}
a:hover, a:focus-visible,
.link-button:hover,
.link-button:focus-visible {
  text-decoration-color: transparent;
  color: var(--link-hover-color);
}
a:active,
.link-button:active {
  color: var(--link-active-color);
}

.link-primary {
  text-decoration: none;
  color: var(--color-primary);
}
.link-primary:hover, .link-primary:focus-visible {
  color: var(--color-secondary);
}
.link-primary:active {
  color: var(--color-accent);
}

button,
input,
optgroup,
select,
textarea {
  font-family: inherit;
  font-size: 100%;
  line-height: 2rem;
}

input[type=radio] {
  -webkit-appearance: none;
}

button,
select {
  text-transform: none;
}

select {
  visibility: hidden;
}

button,
[type=button],
[type=reset],
[type=submit] {
  -webkit-appearance: button;
}

button,
[type=submit] {
  display: block;
}
button:hover,
[type=submit]:hover {
  cursor: pointer;
}

[type=search] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

progress {
  vertical-align: baseline;
}

img,
picture,
video,
canvas,
svg {
  display: block;
  max-width: 100%;
}

img {
  box-sizing: content-box;
  width: 100%;
  height: auto;
}

.picture-svg img {
  width: revert-layer;
}

.inline-svg {
  pointer-events: none;
}

nav ul {
  list-style: none;
}

nav li {
  display: inline-block;
}

address {
  font-style: normal;
}

table {
  text-indent: 0;
  border-color: inherit;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  text-decoration: underline dotted;
}

b,
strong {
  font-family: var(--font-bold);
  font-weight: bolder;
}

code,
kbd,
samp,
pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, "Liberation Mono", Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub,
sup {
  position: relative;
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
}

sub {
  bottom: -0.25em;
}

sup {
  top: -0.5em;
}

summary {
  display: list-item;
}

iframe {
  border: none;
}

::-moz-focus-inner {
  padding: 0;
  border-style: none;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

::-webkit-inner-spin-button,
::-webkit-outer-spin-button {
  height: auto;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

/*! Icons system */
.icon {
  display: inline-block;
  font-size: 2rem;
}

/*! Buttons system */
.button {
  --bg-color: var(--color-secondary);
  --bg-hover-color: var(--color-accent);
  --bg-active-color: var(--color-secondary);
  --color: var(--color-white);
  display: inline-flex;
  justify-content: center;
  align-items: center;
  gap: 1.1rem;
  min-height: 4.8rem;
  padding: 0.5rem 2rem;
  border: none;
  border-radius: 1rem;
  font-family: var(--font-bold);
  font-size: 1.4rem;
  line-height: 1.8rem;
  text-decoration: none;
  text-align: center;
  background-color: var(--bg-color);
  color: var(--color);
  transition-property: transform, opacity, var(--transition-props-color);
  transition-timing-function: var(--transition-func-cubic);
  transition-duration: var(--transition-duration-medium);
  cursor: pointer;
}
.button:hover, .button:focus-visible {
  background-color: var(--bg-hover-color);
  color: var(--color);
}
.button:active {
  background-color: var(--bg-active-color);
  color: var(--color);
}
.button__text {
  position: relative;
  top: -0.1rem;
}

.button--secondary {
  --bg-color: var(--color-primary);
  --bg-hover-color: var(--color-primary-90);
  --bg-active-color: var(--color-primary);
}

.button--outline {
  --bg-color: transparent;
  --bg-hover-color: transparent;
  --bg-active-color: transparent;
  --color: var(--color-primary);
  border: 1px solid var(--color-secondary);
}
.button--outline:hover, .button--outline:focus-visible {
  border-color: var(--color-primary);
}
.button--outline:active {
  border-color: var(--color-secondary);
}

.icon-button {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  width: 1.6rem;
  height: 1.6rem;
  border: none;
  background-color: transparent;
  color: var(--color-secondary);
  transition: color var(--transition-medium-cubic);
}
.icon-button:hover, .icon-button:focus-visible {
  color: var(--color-primary);
}
.icon-button:active {
  color: var(--color-secondary);
}
.icon-button .icon {
  font-size: 1.6rem;
}

.close-button {
  overflow: hidden;
  width: 2rem;
  height: 2rem;
  border: 0;
  background-color: transparent;
  color: transparent;
  opacity: 0.25;
  transition: opacity var(--transition-medium-cubic);
  user-select: none;
}
.close-button:hover {
  opacity: 0.5;
}
.close-button::before, .close-button::after {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  display: block;
  width: 0.2rem;
  height: 100%;
  border-radius: 0.6rem;
  margin: 0 auto;
  background-color: var(--color-black);
  transform: rotate(45deg) translateX(0);
}
.close-button::after {
  transform: rotate(-45deg) translateX(0);
}

.copy-button {
  position: relative;
  display: flex;
  flex-shrink: 0;
  width: 2.8rem;
  height: 2.8rem;
  border: none;
  border-radius: 0.6rem;
  background-image: url("~SVG/ui/copy.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-color: transparent;
  transition: background-color var(--transition-medium-cubic);
}
.copy-button:hover, .copy-button:focus-visible {
  background-color: var(--color-primary-20);
}
.copy-button:active {
  background-color: var(--color-primary-10);
}
.copy-button .button-text {
  position: absolute;
  top: -4.7rem;
  left: -4rem;
  display: none;
  align-items: center;
  min-height: 3.9rem;
  padding: 0.5rem 2rem 0.5rem 4rem;
  border-radius: 1rem;
  font-family: var(--font-bold);
  font-size: 1.2rem;
  line-height: 1.5rem;
  background-image: url("~SVG/ui/check.svg");
  background-position: center left 2rem;
  background-repeat: no-repeat;
  background-color: var(--color-primary);
  color: var(--color-white);
  animation: sublist-appear 0.2s cubic-bezier(0.33, 1, 0.68, 1);
  user-select: none;
}
.copy-button .button-text::after {
  content: "";
  position: absolute;
  bottom: -0.7rem;
  left: calc(50% - 0.6rem);
  width: 0;
  height: 0;
  border-width: 0.7rem 0.6rem 0 0.6rem;
  border-style: solid;
  border-color: var(--color-primary) transparent transparent transparent;
  transform: rotate(0deg);
}
.copy-button .button-text.is-visible {
  display: flex;
}

@keyframes tooltip-appear {
  0% {
    opacity: 0;
  }
}
/*! Common nav rules */
.nav__list {
  display: flex;
  align-items: center;
  gap: 3rem;
  padding-left: 0;
  margin-bottom: 0;
}

.nav__item {
  margin-bottom: 0;
}

.nav__link {
  display: flex;
  align-items: center;
  font-family: var(--font-medium);
  text-decoration: none;
}
.nav__link .icon {
  margin-left: 0.6rem;
  font-size: 2.2rem;
  transition: transform var(--transition-medium-cubic);
}

.nav__sublist {
  display: flex;
  overflow: hidden;
  flex-direction: column;
  gap: 2rem;
  max-height: 0;
  margin-bottom: 0;
  transition: max-height 0.2s linear;
}
@media (min-width: 920px) {
  .nav__sublist {
    position: absolute;
    z-index: 10;
    visibility: hidden;
    height: initial;
    padding: 2rem 4rem;
    border-radius: 2rem;
    margin-top: 1rem;
    margin-left: -4rem;
    background-color: var(--color-white);
    box-shadow: var(--shadow-menu);
    transition: none;
  }
}

.nav__sublist-toggler.is-active .icon {
  transform: rotate(180deg);
}

.nav__sublist .nav__link {
  display: block;
  text-decoration-color: transparent;
}

@media (min-width: 700px) {
  .nav__sublist.is-visible {
    visibility: visible;
    max-height: initial;
    animation: sublist-appear 0.2s cubic-bezier(0.33, 1, 0.68, 1);
  }
}

@keyframes sublist-appear {
  0% {
    top: 0.6rem;
    opacity: 0;
  }
}
/*! Main navbar component */
.navbar {
  display: flex;
}

.navbar-toggler {
  display: none;
}

@media (max-width: 1039.98px) {
  .navbar-toggler {
    position: relative;
    z-index: 101;
    display: block;
    order: 1;
    width: 2.8rem;
    height: 2.8rem;
    padding: 0 0.2rem;
    border: 0;
    background-color: transparent;
  }
  .navbar-toggler__burger {
    display: block;
    width: 2.4rem;
    height: 0.3rem;
    border-radius: 0.3rem;
    background-color: red;
  }
  .navbar-toggler__burger::after, .navbar-toggler__burger::before {
    content: "";
    position: absolute;
    left: 0.2rem;
    width: 2.4rem;
    height: 0.3rem;
    border-radius: 0.3rem;
    background-color: red;
    transition: transform var(--transition-long-cubic);
  }
  .navbar-toggler__burger::before {
    transform: translateY(-0.7rem);
  }
  .navbar-toggler__burger::after {
    transform: translateY(0.7rem);
  }
  .navbar-toggler[aria-expanded=true] .navbar-toggler__burger {
    background-color: transparent;
  }
  .navbar-toggler[aria-expanded=true] .navbar-toggler__burger::after, .navbar-toggler[aria-expanded=true] .navbar-toggler__burger::before {
    border-radius: 0;
  }
  .navbar-toggler[aria-expanded=true] .navbar-toggler__burger::before {
    transform: rotate(45deg);
  }
  .navbar-toggler[aria-expanded=true] .navbar-toggler__burger::after {
    transform: rotate(-45deg);
  }
  .navbar {
    position: fixed;
    top: 0;
    z-index: 100;
    overflow: auto;
    width: min(45rem, 85%);
    height: 100%;
    background-color: var(--color-white);
    transition: transform var(--transition-long-cubic);
  }
  .navbar--rtl {
    right: 0;
    transform: translateX(100%);
  }
  .navbar--ltr {
    left: 0;
    transform: translateX(-100%);
  }
  .navbar--ttb {
    transform: translateY(-100%);
  }
  .navbar--btt {
    top: initial;
    bottom: 0;
    transform: translateY(100%);
  }
  .navbar.is-visible.navbar--rtl, .navbar.is-visible.navbar--ltr {
    transform: translateX(0%);
  }
  .navbar.is-visible.navbar--ttb, .navbar.is-visible.navbar--btt {
    transform: translateY(0%);
  }
  .navbar .nav-container {
    display: flex;
    flex-grow: 1;
    flex-direction: column;
    align-items: center;
    padding-top: 0;
  }
  .navbar .nav__list {
    flex-direction: column;
    align-items: initial;
    padding: min(20vh, 10rem) 3rem;
  }
  body.is-fixed {
    position: fixed;
    width: 100%;
  }
  body.has-scroll {
    overflow-y: scroll;
    overflow-x: unset;
  }
}
/*! Overlay component */
.overlay {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 99;
  visibility: hidden;
  width: 100%;
  height: 100%;
  background-color: var(--color-overlay-bg);
  opacity: 0;
  transition: var(--transition-overlay);
  cursor: pointer;
}

.overlay.is-visible {
  visibility: visible;
  opacity: 1;
}

/*! Swiper.js component */
.swiper {
  user-select: none;
}

.swiper-button-next,
.swiper-button-prev {
  outline: 0;
}

.swiper-button-next:after,
.swiper-button-prev:after {
  content: "";
  width: 2.5rem;
  height: 3rem;
  background: url("~SVG/ui/arrow.svg") no-repeat 0 50%;
}

.swiper-button-prev:after {
  transform: scaleX(-1);
}

.swiper-controls {
  position: absolute;
  right: 2rem;
  bottom: 0.5rem;
  z-index: 1;
  width: 14rem;
  height: 3rem;
  border-radius: 2rem;
  background-color: var(--color-white);
}
.swiper-controls .swiper-button-next,
.swiper-controls .swiper-button-prev {
  top: 0;
  right: 0;
  z-index: 1;
  width: 2.5rem;
  height: 3rem;
  margin-top: 0;
}
.swiper-controls .swiper-button-prev {
  left: 0;
}
.swiper-controls .swiper-pagination {
  position: initial;
}

.swiper--reverse {
  transform: rotate(180deg);
}
.swiper--reverse .swiper-slide,
.swiper--reverse .swiper-controls {
  transform: rotate(-180deg);
}

/*! Forms and their elements */
.form--flex {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
@media (min-width: 700px) {
  .form--flex {
    gap: 2rem;
  }
}

.form-panel {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
  padding: 2rem;
  border-radius: 2rem;
  background-color: var(--color-white);
}
@media (min-width: 920px) {
  .form-panel {
    padding: 3rem;
  }
}

.form__fieldset.form-panel--border-legend legend {
  padding-bottom: 1.5rem;
  border-bottom: 1px solid var(--color-primary-05);
  margin-bottom: 0;
}

.form__fieldset {
  border: 0;
}
.form__fieldset--flex {
  display: flex;
  flex-wrap: wrap;
  gap: 1.6rem;
}
.form__fieldset--flex .form__field {
  flex: 1 0 auto;
  min-width: 28rem;
}
@media (max-width: 359.98px) {
  .form__fieldset--flex .form__field {
    min-width: 25rem;
  }
}
.form__fieldset legend {
  float: left;
  margin-bottom: 0.5rem;
  font-family: var(--font-bold);
  font-size: 2rem;
  line-height: 2.5rem;
}
.form__fieldset--2-col {
  display: flex;
  flex-wrap: wrap;
  gap: 1.6rem;
}
@media (min-width: 575px) {
  .form__fieldset--2-col {
    padding-bottom: 1.5rem;
    border-bottom: 1px solid var(--color-primary-05);
  }
}
.form__fieldset--2-col > .form__field {
  flex: 1 0 0;
  min-width: 22rem;
}
.form__fieldset--last-child {
  padding-bottom: 0;
  border: none;
}
@media (min-width: 575px) {
  .form__fieldset--last-child {
    padding-bottom: 0;
    border: none;
  }
}

.form__footnote {
  margin-bottom: 0;
  font-size: 12px;
  line-height: 15px;
}

.form__field {
  position: relative;
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}
.form__field:last-child {
  margin-bottom: 0;
}
.form__field--border {
  padding-bottom: 1.5rem;
  border-bottom: 1px solid var(--color-primary-05);
  margin-bottom: 0;
}

.form__field--checkbox {
  flex-direction: row;
  align-items: center;
  min-height: 2rem;
}
.form__field--checkbox .form__label--checkbox {
  width: initial;
  font-size: 1.6rem;
  line-height: 2rem;
}

.form__label,
.form-label {
  display: inline-block;
  order: -1;
  width: 100%;
  font-size: 1.2rem;
  line-height: 1.5rem;
}

.form__text-input {
  width: 100%;
  min-height: 4.8rem;
  padding: 1rem 5.8rem 1rem 2rem;
  border: 1px solid var(--color-primary-20);
  border-radius: 1rem;
}
.form__text-input:focus-visible::placeholder {
  color: transparent;
}
.form__text-input::placeholder {
  color: var(--color-primary-50);
}

.input-textarea {
  min-height: 9.6rem;
  max-height: 20rem;
  resize: vertical;
}

.input__file {
  opacity: 0;
}

.checkbox-wrapper {
  display: flex;
  align-items: center;
  min-height: 6.4rem;
}

.form__checkbox {
  position: absolute;
  opacity: 0;
}
.form__checkbox:checked + .form__label--checkbox::after {
  display: flex;
}

.form__label--checkbox:hover::before, .form__label--checkbox:focus-visible::before,
.form-radio:hover::before,
.form-radio:focus-visible::before {
  border-color: var(--color-primary-75);
}
.form__label--checkbox:active::before,
.form-radio:active::before {
  background-color: var(--color-primary-05);
}
.form__label--checkbox::before,
.form-radio::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  width: 2rem;
  height: 2rem;
  border: 1px solid var(--color-primary-20);
  border-radius: 50%;
  background-color: transparent;
  transition: border-color var(--transition-medium-cubic), background-color var(--transition-medium-cubic);
}
.form__label--checkbox::after,
.form-radio::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  display: none;
  flex-shrink: 0;
  width: 2rem;
  height: 2rem;
  border-radius: 50%;
  background-image: url("~SVG/ui/check.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-color: var(--color-primary);
  transition: background-color var(--transition-medium-cubic);
}

.form__label--checkbox {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-left: 3rem;
  user-select: none;
  cursor: pointer;
}
.form__label--checkbox::before, .form__label--checkbox::after {
  top: 50%;
  transform: translateY(-50%);
}

.form__label-title {
  margin-bottom: 0.2rem;
  font-family: var(--font-bold);
  font-size: 1.3rem;
  line-height: 1.6rem;
}

.form__label--checkbox a {
  display: inline;
}

.form__submit {
  gap: 1rem;
  width: fit-content;
}
.form__submit .icon {
  font-size: 1.5rem;
}
.form__submit--wide {
  width: 100%;
}

.select-wrapper {
  position: relative;
  width: 100%;
}
.select-wrapper__chevron {
  position: absolute;
  top: 1.9rem;
  right: 2.1rem;
  font-size: 1rem;
  transition: transform var(--transition-medium-cubic), color var(--transition-medium-cubic);
}

.custom-field {
  min-height: 6.4rem;
}

.captcha {
  aspect-ratio: 29.1/7.4;
  max-width: 29.1rem;
  background-image: url("~Images/captcha.jpg?&size=291");
  background-image: image-set(url("~Images/captcha.jpg?&size=291") 1x, url("~Images/captcha.jpg?") 2x);
  background-size: contain;
}
.webp .captcha {
  background-image: url("~Images/captcha.jpg?format=webp&size=291");
  background-image: image-set(url("~Images/captcha.jpg?format=webp&size=291") 1x, url("~Images/captcha.jpg?format=webp") 2x);
}

.form__caption {
  font-size: 1.4rem;
  line-height: 2.2rem;
}
.form__caption p {
  margin-bottom: 1rem;
}
.form__caption p:last-child {
  margin-bottom: 0;
}

.qty-input {
  display: flex;
  align-items: center;
}

.qty-input__qty-field {
  width: 100%;
  border: none;
  font-size: 1.6rem;
  text-align: center;
}

.qty-input__minus,
.qty-input__plus {
  flex-shrink: 0;
  width: 2.4rem;
  height: 2.4rem;
  border: none;
  background-image: url("~SVG/ui/minus.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-color: transparent;
  color: transparent;
  transition: opacity var(--transition-medium-cubic);
  user-select: none;
}

.qty-input__plus {
  background-image: url("~SVG/ui/plus.svg");
}

.form-radio-wrapper {
  position: relative;
  width: 100%;
}

.form-radio-input {
  position: absolute;
  visibility: hidden;
}
.form-radio-input:checked + .form-radio::after {
  display: flex;
}

.form-radio {
  position: relative;
  display: flex;
  flex-direction: column;
  padding-left: 3rem;
  cursor: pointer;
}

.form-radio__title {
  margin-bottom: 0.2rem;
  font-family: var(--font-bold);
  font-size: 1.3rem;
  line-height: 1.6rem;
}

.form-radio__text {
  font-size: 1.2rem;
  line-height: 1.5rem;
}

.form-radio__price {
  position: absolute;
  top: 0;
  right: 0;
  font-size: 1.2rem;
  line-height: 1.5rem;
  color: var(--color-secondary);
}

/* Borderless form */
.form--secondary .form__text-input {
  border: none;
}

/* Search form */
.search-form {
  position: relative;
  display: flex;
  width: 100%;
}
@media (min-width: 1240px) {
  .search-form {
    max-width: 45.3rem;
  }
}
.search-form .form__label:focus-within + .search-button {
  color: var(--color-primary);
}

.search-button {
  position: absolute;
  top: 50%;
  right: 2rem;
  width: 1.8rem;
  height: 1.8rem;
  border: none;
  background-color: transparent;
  color: var(--color-secondary);
  transform: translateY(-50%);
  transition: color var(--transition-medium-cubic);
}
.search-button .icon {
  font-size: 1.8rem;
}

/* Subscribe form */
.subscribe-form {
  display: flex;
  flex-grow: 1;
  gap: 1rem;
}
@media (min-width: 800px) {
  .subscribe-form {
    max-width: 42rem;
  }
}
.subscribe-form .form__field {
  margin-bottom: 0;
}
.subscribe-form .form__field:first-child {
  flex-grow: 1;
}
@media (max-width: 499.98px) {
  .subscribe-form .form__submit {
    overflow: hidden;
    width: 4.8rem;
    background-image: url("~SVG/ui/arrow.svg");
    background-position: center;
    background-repeat: no-repeat;
    color: transparent;
  }
}

/*! Pop-ups & modal dialogs */
.dialog {
  min-width: 32rem;
  width: 100%;
  max-width: 70.8rem;
  height: auto;
  padding: 2rem;
  border: none;
  border-radius: 2rem;
  margin: auto;
  background-color: transparent;
}
@media (max-width: 699.98px) {
  .dialog {
    padding: 1rem;
  }
}
.dialog[data-modal=false] {
  margin-top: 10rem;
}

.dialog[open] {
  animation: dialogSlideInUp 0.25s cubic-bezier(0.71, 1.7, 0.77, 1.24) forwards;
}

.dialog.close {
  animation: dialogSlideOutDown 0.25s ease backwards;
}

@keyframes dialogSlideInUp {
  from {
    opacity: 0;
    transform: translate3d(0, 4rem, 0);
  }
  to {
    opacity: 1;
    transform: translate3d(0, 0, 0);
  }
}
@keyframes dialogSlideOutDown {
  from {
    opacity: 1;
    transform: translate3d(0, 0, 0);
  }
  to {
    opacity: 0;
    transform: translate3d(0, 8rem, 0);
  }
}
.dialog[open]::backdrop {
  background-color: var(--color-overlay-bg);
  animation: backdropFadeIn 0.3s ease forwards;
}

.dialog.close::backdrop {
  animation: backdropFadeOut 0.3s ease backwards;
}

@keyframes backdropFadeIn {
  from {
    background-color: transparent;
  }
  to {
    background-color: var(--color-overlay-bg);
  }
}
@keyframes backdropFadeOut {
  from {
    background-color: var(--color-overlay-bg);
  }
  to {
    background-color: transparent;
  }
}
.dialog__inner-wrapper {
  position: relative;
  top: 50%;
  z-index: 9999;
  padding: 3rem;
  border-radius: 2rem;
  background-color: var(--color-white);
  box-shadow: var(--shadow-dialog);
  transform: translateY(-50%);
}

.dialog__close-button {
  position: absolute;
  top: 1.2rem;
  right: 1.2rem;
}

.dialog__header {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.5rem;
  margin-bottom: 2rem;
}

.dialog__title {
  font-family: var(--font-bold);
  font-size: 3rem;
  line-height: 3.8rem;
}

.dialog__note {
  font-size: 1.6rem;
  text-align: center;
}

/* Callback form */
.callback-form,
.callback-push-form {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
  max-width: 30rem;
  margin: 0 auto;
}
.callback-form .form__field,
.callback-push-form .form__field {
  width: 100%;
}
.callback-form .form__submit,
.callback-push-form .form__submit {
  margin: 0 auto;
}

.callback-push-form .form__submit {
  margin: initial;
}

.callback-push-submit {
  flex-direction: row;
  justify-content: center;
  gap: 1rem;
}
.callback-push-submit .button {
  flex: 1 0 0;
  max-width: 9.2rem;
}

/*! Dropdown lists */
.dropdown {
  position: relative;
  display: flex;
}

.dropdown__button {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  width: 2rem;
  height: 2rem;
  border: none;
  background-color: transparent;
  color: var(--color-primary);
  transition: color var(--transition-medium-cubic);
}
.dropdown__button:hover, .dropdown__button:focus-visible {
  color: var(--color-secondary);
}
.dropdown__button:active {
  color: var(--color-accent);
}
.dropdown__button .icon {
  font-size: 1.8rem;
}

.dropdown-list {
  position: absolute;
  top: calc(100% + 1.4rem);
  left: 0;
  z-index: 3;
  display: none;
  flex-direction: column;
  gap: 1rem;
  padding: 1rem 0;
  border-top: 1px solid var(--color-secondary);
  border-radius: 0 0 1.5rem 1.5rem;
  margin: 0;
  list-style: none;
  background-color: var(--color-white);
  box-shadow: var(--shadow-menu);
  transform: translateX(calc(-50% + 1rem));
  animation: sublist-appear 0.2s cubic-bezier(0.33, 1, 0.68, 1);
}
.dropdown-list.is-visible {
  display: flex;
}

.dropdown-item {
  min-width: 16rem;
  width: max-content;
  max-width: 26rem;
  padding: 0 2rem 1rem;
  border-bottom: 1px solid var(--color-primary-05);
  margin-bottom: 0;
  line-height: 1.6rem;
}
.dropdown-item:last-child {
  padding-bottom: 0;
  border: none;
}

/* Custom select component */
.custom-select-container {
  flex-grow: 1;
  border: 1px solid var(--color-primary-20);
  border-radius: 1rem;
  font-size: 1.4rem;
  user-select: none;
}
.custom-select-container select {
  width: 1rem;
}

.custom-select-opener {
  position: relative;
  z-index: 2;
  display: flex;
  overflow: hidden;
  align-items: center;
  width: 100%;
  height: 4.6rem;
  padding-left: 1.9rem;
  border-radius: 4rem;
}

.custom-select-opener[aria-expanded=true] {
  z-index: 4;
}

.custom-select-panel {
  top: calc(100% + 0.1rem);
  left: 0;
  z-index: 1;
  display: none;
  padding: 1rem 2rem 2rem;
  border-radius: 0 0 1.5rem 1.5rem;
  background-color: var(--color-white);
  box-shadow: var(--shadow-menu);
}

.select-wrapper .custom-select-panel {
  left: -0.1rem;
  width: calc(100% + 0.2rem);
}

.custom-select-container.is-open .custom-select-panel {
  z-index: 3;
  display: block;
  max-height: 23rem;
  animation: hide-scroll 0.15s backwards;
  animation: sublist-appear 0.2s cubic-bezier(0.33, 1, 0.68, 1);
}
.custom-select-container.is-open + .select-wrapper__chevron {
  color: var(--color-secondary);
  transform: rotate(-180deg);
}

@keyframes hide-scroll {
  from, to {
    overflow: hidden;
  }
}
.custom-select-option {
  padding: 1rem 0 0.9rem;
  border-bottom: 1px solid var(--color-primary-05);
  font-size: 1.4rem;
  line-height: 1.8rem;
}
.custom-select-option.is-selected {
  font-weight: 400;
}
.custom-select-option.has-focus {
  background-color: transparent;
  color: var(--color-secondary);
}

.custom-select-option[data-value=""] {
  display: none;
}

.header__controls .custom-select-container {
  display: flex;
  border: none;
  font-family: var(--font-bold);
  font-size: 1.3rem;
}
.header__controls .custom-select-container:hover ~ .icon, .header__controls .custom-select-container:focus-visible ~ .icon {
  color: var(--color-secondary);
}
.header__controls .custom-select-container:active ~ .icon {
  color: var(--color-accent);
}
.header__controls .custom-select-container.is-open ~ .icon {
  color: var(--color-secondary);
}
.header__controls .custom-select-container.is-open ~ .header-select-wrapper__chevron {
  transform: rotate(-180deg);
}
.header__controls .custom-select-opener {
  height: 2rem;
  padding-right: 1.4rem;
  padding-left: 2.4rem;
}
@media (min-width: 920px) {
  .header__controls .custom-select-opener {
    padding-right: 1.8rem;
    padding-left: 2.8rem;
  }
}
.header__controls .custom-select-panel {
  top: calc(100% + 1.4rem);
  left: calc(-14rem + 7rem + 50%);
  width: initial;
  padding: 1rem 0;
  border-top: 1px solid var(--color-secondary);
}
@media (max-width: 359.98px) {
  .header__controls .custom-select-panel {
    left: -80%;
  }
}
.header__controls .custom-select-option {
  min-width: 14rem;
  width: max-content;
  max-width: 26rem;
  padding: 0 2rem 1rem;
  border-bottom: 1px solid var(--color-primary-05);
  margin-bottom: 1rem;
  font-size: 1.3rem;
  line-height: 1.6rem;
  text-align: center;
  transition: color var(--transition-medium-cubic);
}
.header__controls .custom-select-option:hover, .header__controls .custom-select-option:focus-visible {
  color: var(--color-secondary);
}
.header__controls .custom-select-option:active {
  color: var(--color-accent);
}
.header__controls .custom-select-option:last-child {
  padding-bottom: 0;
  border-bottom: none;
  margin-bottom: 0;
}

.header-select-wrapper {
  position: relative;
  display: flex;
}
.header-select-wrapper__icon {
  position: absolute;
  transition: color var(--transition-medium-cubic);
}
.header-select-wrapper__chevron {
  position: absolute;
  top: 0.5rem;
  right: 0;
  font-size: 1rem;
  transition: all var(--transition-medium-cubic);
}

/*! Accordion component */
.accordion-button {
  position: relative;
  cursor: pointer;
}
.accordion-button::after {
  content: "";
  position: absolute;
  top: 2.2rem;
  right: 0;
  width: 2.4rem;
  height: 2.4rem;
  transition: all 0.2s ease-in-out;
}

.accordion-button[aria-expanded=true]::after {
  transform: rotate(180deg);
}

.accordion-panel {
  display: grid;
  grid-template-rows: 0fr;
  transition: grid-template-rows 0.2s ease-out;
}

.accordion-button[aria-expanded=true] + .accordion-panel {
  grid-template-rows: 1fr;
}

.accordion-content {
  overflow: hidden;
}

@media (min-width: 700px) {
  .accordion--mobile .accordion-button {
    cursor: default;
    pointer-events: none;
  }
  .accordion--mobile .accordion-panel {
    grid-template-rows: 1fr;
  }
}

/*! Various card & panel views */
.card {
  border-radius: 2rem;
  background-color: var(--color-white);
  transition: box-shadow var(--transition-medium-cubic);
}
.card:hover {
  box-shadow: var(--shadow-card);
}

.panel {
  width: 100%;
  padding: 2rem;
  border-radius: 2rem;
  background-color: var(--color-white);
}
@media (min-width: 1240px) {
  .panel {
    padding: 3rem;
  }
}
.panel .h2 {
  padding-bottom: 1rem;
  font-family: var(--font-bold);
  font-size: 2rem;
  line-height: 2.5rem;
}

/* Product cards */
.product-card {
  display: flex;
  flex-direction: column;
  padding: 0 1rem 1rem;
}
@media (min-width: 700px) {
  .product-card {
    gap: 1rem;
    padding: 2rem;
  }
}
@media (min-width: 1240px) {
  .product-card {
    padding: 3rem;
  }
}

.product-card__img:hover + .product-card__heading .product-card__brand, .product-card__img:focus-visible + .product-card__heading .product-card__brand {
  color: var(--color-secondary);
}
.product-card__img:active {
  color: var(--color-accent);
}
.product-card__img img {
  width: 14rem;
  height: 14rem;
}

.product-card__heading {
  margin-bottom: 0;
}
@media (max-width: 359.98px) {
  .product-card__heading + .product-card__controls {
    margin-top: 1rem;
  }
}

.product-card__brand {
  font-family: var(--font-bold);
  font-size: 1.8rem;
  line-height: 2.3rem;
  text-decoration: none;
  color: var(--color-primary);
}
@media (min-width: 700px) {
  .product-card__brand {
    font-size: 2.4rem;
    line-height: 3rem;
  }
}

.product-card__ingredient {
  width: fit-content;
  text-decoration: none;
}
@media (max-width: 699.98px) {
  .product-card__ingredient {
    font-size: 1.2rem;
    line-height: 1.5rem;
  }
}
@media (min-width: 700px) {
  .product-card__ingredient {
    margin-top: -1.2rem;
  }
}
.product-card__ingredient:hover, .product-card__ingredient:focus-visible {
  color: var(--color-primary);
}
.product-card__ingredient:active {
  color: var(--color-secondary);
}

.product-card__text {
  font-size: 1.3rem;
}
@media (max-width: 699.98px) {
  .product-card__text {
    margin: 1rem 0;
    font-size: 1rem;
    line-height: 1.6rem;
  }
}

.product-card__controls {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: auto;
}

@media (max-width: 699.98px) {
  .product-card__button {
    padding: 0.5rem 1.4rem;
  }
  .product-card__button .button__text {
    display: none;
  }
}

.product-card__price {
  font-family: var(--font-bold);
  font-size: 1.6rem;
  line-height: 2rem;
  color: var(--color-secondary);
}
@media (min-width: 700px) {
  .product-card__price {
    font-size: 1.8rem;
    line-height: 2.3rem;
  }
}

/* Combo cards */
.combo-cards {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}
@media (max-width: 359.98px) {
  .combo-cards {
    display: flex;
    flex-direction: column;
  }
}
@media (max-width: 574.98px) {
  .combo-cards {
    grid-column: span 2;
    grid-row: 2;
    flex-direction: row;
    gap: 1rem;
  }
}

.combo-card {
  flex: 1 0 0;
  background-image: url("~SVG/ui/happy-day.svg");
  background-position: center right 3rem;
  background-repeat: no-repeat;
}
@media (max-width: 389.98px) {
  .combo-card {
    background-position: center right 1.4rem;
  }
}
@media (max-width: 699.98px) {
  .combo-card {
    background-position: center right 2.1rem;
    background-size: 4.4rem;
  }
}
.combo-card--sale {
  background-image: url("~SVG/ui/sale.svg");
  background-position: calc(100% - 3.1rem) calc(50% - 0.1rem);
}
@media (max-width: 699.98px) {
  .combo-card--sale {
    background-position: calc(100% - 2.1rem) 50%;
  }
}
.combo-card--sale .combo-card__title {
  line-height: 2.4rem;
}
@media (max-width: 699.98px) {
  .combo-card--sale .combo-card__title {
    margin-top: -0.5rem;
  }
}

.combo-card__link-wrapper {
  width: 100%;
  height: 100%;
  padding: 2rem 6rem 1.4rem 2rem;
}
@media (min-width: 575px) {
  .combo-card__link-wrapper {
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 1rem;
  }
}
@media (min-width: 700px) {
  .combo-card__link-wrapper {
    justify-content: initial;
    gap: 0;
  }
}
@media (min-width: 1240px) {
  .combo-card__link-wrapper {
    padding: 3rem 10.4rem 3rem 3rem;
  }
}

.combo-card__title {
  padding-right: 2rem;
  margin-bottom: 0.2rem;
  font-family: var(--font-bold);
  font-size: 1.8rem;
  line-height: 2.4rem;
}
@media (min-width: 700px) {
  .combo-card__title {
    padding-right: 0;
    margin: 2.4rem 0 0.9rem;
    font-size: 2.4rem;
    line-height: 3rem;
  }
}

.combo-card__text {
  line-height: 1.8rem;
}
@media (max-width: 699.98px) {
  .combo-card__text {
    font-size: 1.2rem;
    line-height: 1.5rem;
  }
}

/* Testimonials */
.testimonial {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
  padding: 3rem;
}

.testimonial__header {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}

.testimonial__author {
  font-family: var(--font-bold);
  font-size: 1.8rem;
  line-height: 2.3rem;
}

.testimonial__text {
  font-size: 1.3rem;
}

/* Rating */
.rating {
  display: flex;
  gap: 0.2rem;
}

.rating__star {
  width: 1.8rem;
  height: 1.8rem;
  background-image: url("~SVG/ui/star.svg");
  background-position: center;
  background-repeat: no-repeat;
}

/* Product info panels */
.info-panel {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
.info-panel a {
  display: inline;
}
.info-panel a:hover, .info-panel a:focus-visible {
  color: var(--color-primary);
}
.info-panel a:active {
  color: var(--color-secondary);
}

.info-panel__image {
  width: 20rem;
  height: 20rem;
}

.info-panel--gift .info-panel__image {
  width: initial;
  height: 13.7rem;
  margin: 0 auto;
}

/* Cart info panels */
.cart-panel {
  display: flex;
  flex-wrap: wrap;
  gap: 3rem;
}
@media (min-width: 700px) {
  .cart-panel {
    gap: 2rem;
  }
}
@media (min-width: 1240px) {
  .cart-panel {
    flex-direction: column;
    gap: 0;
  }
}

.cart-panel-item {
  display: flex;
  flex-basis: 100%;
  flex-direction: column;
  justify-content: center;
  gap: 0.5rem;
}
@media (min-width: 700px) {
  .cart-panel-item {
    flex-basis: calc(50% - 1rem);
  }
}
@media (min-width: 1240px) {
  .cart-panel-item {
    flex-basis: initial;
    padding-bottom: 2rem;
