/*! Swiper.js component */

@use '../abstracts' as *;

.swiper {
  user-select: none;
}

.swiper-button-next,
.swiper-button-prev {
  outline: 0;
}

.swiper-button-next:after,
.swiper-button-prev:after {
  content: '';
  width: 2.5rem;
  height: 3rem;
  background: url('~SVG/ui/arrow.svg') no-repeat 0 50%;
}

.swiper-button-prev:after {
  transform: scaleX(-1);
}

.swiper-controls {
  position: absolute;
  right: 2rem;
  bottom: 0.5rem;
  z-index: 1;
  width: 14rem;
  height: 3rem;
  border-radius: 2rem;
  background-color: var(--color-white);

  .swiper-button-next,
  .swiper-button-prev {
    top: 0;
    right: 0;
    z-index: 1;
    width: 2.5rem;
    height: 3rem;
    margin-top: 0;
  }

  .swiper-button-prev {
    left: 0;
  }

  .swiper-pagination {
    position: initial;
  }
}

.swiper--reverse {
  transform: rotate(180deg);
  .swiper-slide,
  .swiper-controls {
    transform: rotate(-180deg);
  }
}
