/*! Main header rules */

@use '../abstracts' as *;

.header {
  --gradient-position: 29.6rem;

  padding: 1.5rem 0 1rem;
  background: linear-gradient(180deg, var(--color-white) var(--gradient-position), var(--color-light-blue) var(--gradient-position));
  @include bp(md) {
    --gradient-position: 26.8rem;
  }
  @include bp(xl) {
    --gradient-position: 24.1rem;
  }

  .container {
    position: relative;
    display: flex;
    flex-direction: column;
    &::before,
    &::after {
      content: '';
      position: absolute;
      top: 2.8rem;
      z-index: 1;
      width: calc(100% - 3rem);
      max-width: 103.5rem;
      height: 0.1rem;
      background-color: var(--color-primary-05);
      @include bp(md) {
        width: calc(100% - 4rem);
      }
      @include bp(xl) {
        top: 3.4rem;
      }
    }
    &::after {
      display: none;
      @include bp(xl) {
        top: 11.2rem;
        display: block;
      }
    }
  }
}

.header__grid-wrapper {
  position: relative;
  z-index: 2;
  display: grid;
  grid-template-columns: 20.1rem auto;
  @include bp(xl) {
    grid-template-columns: 23.4rem 49.6rem auto;
    grid-template-rows: 3.5rem 7.8rem;
  }
}

.header__phones {
  position: relative;
  grid-column: span 2;
  display: flex;
  align-items: flex-start;
  gap: 1.1rem;
  max-width: 100rem;

  & > .header-phone {
    position: relative;
    top: 0.2rem;
  }

  .dropdown {
    position: absolute;
    right: 0.3rem;
    .header-phone {
      font-family: var(--font-regular);
    }
  }

  .dropdown__button {
    display: none;
    &.is-visible {
      display: inline-flex;
    }
  }

  .dropdown-list {
    transform: translateX(calc(-100% + 3rem));
    @include bp(md) {
      transform: translateX(calc(-100% + 2rem));
    }
    @include bp(xl) {
      transform: translateX(calc(-50% + 1rem));
    }
  }
}

.header__phones-caption {
  position: relative;
  top: 0.2rem;
  flex-shrink: 0;
  font-family: var(--font-bold);
  font-size: 1.3rem;
  line-height: 1.6rem;
  .link-button {
    line-height: 1.6rem;
  }
  .caption-text {
    @include bp(xs) {
      display: none;
    }
  }
}

.header__phones-wrapper {
  display: flex;
  overflow: hidden;
  align-items: center;
  min-width: 0;
  width: 100%;
  padding: 0;
  margin-bottom: 0;
  @include bp-max(359.98px) {
    justify-content: initial;
  }
  @include bp(xs) {
    justify-content: center;
  }

  .dropdown-item {
    position: relative;
    top: 0.2rem;
    flex-shrink: 0;
    min-width: 12.1rem;
    width: initial;
    max-width: initial;
    padding: 0;
    border: none;
    list-style: none;
    .header-phone {
      padding: 0 0.5rem;
      white-space: nowrap;
    }
    &:first-child .header-phone {
      padding-left: 0;
    }
    &:last-child .header-phone {
      padding-right: 0;
    }
  }
}

.header-phone {
  font-family: var(--font-medium);
  font-size: 1.3rem;
  line-height: 1.6rem;
  text-decoration-color: transparent;
  color: var(--color-primary);
  &:hover,
  &:focus-visible {
    color: var(--color-secondary);
  }
  &:active {
    color: var(--color-accent);
  }
}

.header__controls {
  grid-column: 2;
  display: flex;
  align-items: center;
  gap: 1rem;
  min-height: 2rem;
  margin-top: 1rem;
  @include bp(xs) {
    flex-direction: column;
    justify-content: center;
    gap: 0.5rem;
  }
  @include bp(lg) {
    gap: 2rem;
    margin-top: 0;
  }
  @include bp(xl) {
    grid-column: 3;
    align-items: flex-start;
  }
}

.header__lang {
  @include bp(lg) {
    min-width: 9.1rem;
  }
}

.header__currency {
  @include bp(lg) {
    min-width: 7.4rem;
  }
}

.header__currency .custom-select-panel {
  @include bp-min-max(389.98px, 430px) {
    left: calc(-14rem + 7rem);
  }
}

.header__auth {
  display: none;
  gap: 0.5rem;
  font-family: var(--font-bold);
  font-size: 1.3rem;
  text-decoration: none;
  color: var(--color-primary);
  @include bp(md) {
    display: inline-flex;
  }

  &:hover,
  &:focus-visible {
    color: var(--color-primary);
    .icon {
      color: var(--color-secondary);
    }
  }
  &:active {
    color: var(--color-primary);
    .icon {
      color: var(--color-accent);
    }
  }

  .icon {
    transition: color var(--transition-medium-cubic);
  }
}

.logo {
  display: grid;
  grid-template-columns: 4rem auto;
  grid-column: 1;
  grid-row: 2;
  column-gap: 0.2rem;
  flex-shrink: 0;
  min-width: 16.4rem;
  height: 5.5rem;
  padding: 1rem 0;
  margin-top: 1.2rem;
  text-decoration: none;
  color: var(--color-primary);
  @include bp(lg) {
    margin-bottom: 1rem;
  }
  @include bp(xl) {
    column-gap: 1rem;
    height: 7.8rem;
    padding: 1.5rem 0;
    margin-top: 0.2rem;
    margin-bottom: 0;
  }
}

.logo__image {
  grid-row: 1 / span 2;
  width: 3.4rem;
  height: 3.4rem;
  @include bp(xl) {
    width: 4rem;
    height: 4rem;
  }
}

.logo__title {
  font-family: var(--font-bold);
  font-size: 1.7rem;
  line-height: 2.2rem;
  @include bp(xl) {
    font-size: 2rem;
    line-height: 2.5rem;
  }
}

.logo__text {
  font-size: 1.1rem;
  line-height: 1.8rem;
  @include bp(xl) {
    font-size: 1.3rem;
    line-height: 1.6rem;
  }
}

.header__search-wrapper {
  grid-column: span 2;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 1rem;
  max-width: calc(100% - 16rem);
  @include bp(sm) {
    grid-column: span 2;
    max-width: initial;
  }
  @include bp(xl) {
    max-width: 80.2rem;
  }
}

.categories-button {
  flex-shrink: 0;
  @include bp(sm) {
    display: none;
  }

  .icon {
    font-size: 2.4rem;
  }

  .button__text {
    display: none;
    @include bp-min(800px) {
      display: block;
    }
  }
}

.cart-button {
  position: relative;
  flex-shrink: 0;
  @include bp(sm) {
    display: none;
  }

  .icon {
    width: 2rem;
  }

  .button__text {
    display: none;
    @include bp(lg) {
      display: block;
    }
  }

  .button__total {
    position: relative;
    top: -0.1rem;
    display: none;
    color: var(--color-secondary);
    @include bp(lg) {
      display: block;
    }
  }
}

.footer-button--cart::after {
  content: 't';
}

.cart-button::after,
.footer-button--cart::after {
  content: attr(data-counter);
  position: absolute;
  top: 0.5rem;
  left: 2.9rem;
  display: flex;
  justify-content: center;
  align-items: center;
  min-width: 1.8rem;
  height: 1.8rem;
  border-radius: 50%;
  font-size: 1.2rem;
  line-height: 1.5rem;
  background-color: var(--color-secondary);
}

.cart-button:not([data-counter])::after,
.footer-button--cart:not([data-counter])::after {
  display: none;
}

.header__info {
  position: relative;
  z-index: 1;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 1rem;
  max-width: 96.5rem;
  min-height: 4.8rem;
  margin-top: 1rem;
  @include bp(md) {
    flex-direction: row;
    gap: 2rem;
  }
  @include bp(lg) {
    margin-top: 1.5rem;
  }
}

.customer-choise {
  display: grid;
  grid-template-columns: 4.2rem 1fr;
  column-gap: 1rem;
  max-width: 26rem;
  @include bp(lg) {
    max-width: initial;
  }
}

.customer-choise__logo {
  grid-row: 1 / span 2;
  align-self: center;
  @include bp(lg) {
    align-self: initial;
    margin-top: 0.3rem;
  }
}

.customer-choise__counter {
  font-family: var(--font-bold);
  font-size: 2.6rem;
  line-height: 3.3rem;
}

.customer-choise__text {
  font-size: 1.2rem;
  line-height: 1.5rem;
  color: var(--color-primary-75);
}

.header-brands {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  max-width: 25rem;
  margin-right: auto;
  @include bp(sm) {
    min-height: 2.4rem;
  }
  @include bp(md) {
    gap: 2rem;
    max-width: initial;
  }
  @include bp(lg) {
    margin-right: 0;
  }
}

.header-nav {
  position: relative;
  margin-top: 2.1rem;
  @include bp(md) {
    margin-top: 2.6rem;
  }

  .nav__list {
    position: relative;
    z-index: 1;
    gap: 0;
    padding: 0 2.4rem 0 0.6rem;
    border-radius: 1rem;
    background-color: var(--color-primary);
    outline: 0.8rem solid var(--color-light-blue);
    @include bp-min(800px) {
      padding: 0;
    }
  }
  .nav__item {
    display: flex;
    flex: 1 0 auto;
    justify-content: center;
    align-items: center;
    min-height: 4.8rem;
    border-right: 1px solid var(--color-white-05);
    margin-right: -0.1rem;
    @include bp(xl) {
      flex-basis: 0;
    }

    &:last-child {
      border: none;
      margin-right: 0;
    }
  }
  .nav__link {
    padding: 0 1.2rem;
    font-family: var(--font-bold);
    font-size: 1.2rem;
    color: var(--color-white);
    @include bp-min(800px) {
      font-size: 1.4rem;
    }
    &:hover,
    &:focus-visible {
      color: var(--color-secondary);
    }
    &:active {
      color: var(--color-accent);
    }
  }
  &::after {
    content: '';
    position: absolute;
    right: 0;
    bottom: 1.6rem;
    width: 13.8rem;
    height: 15rem;
    @include bg-picture('doctor.jpg', 276);
    background-position: bottom right;
    background-repeat: no-repeat;
    background-size: contain;
    @include bp-max(359.98px) {
      display: none;
    }
    @include bp(md) {
      right: 0.5rem;
      bottom: 2.2rem;
      width: 21.4rem;
      height: 23.3rem;
      @include bg-picture('doctor-md.jpg', 428);
    }
  }

  .hidden-links {
    gap: 0;
    min-width: 14rem;
    .nav__item {
      min-height: initial;
      &:last-child .nav__link {
        padding-bottom: 0;
        border: none;
        margin-bottom: 0;
      }
    }
    .nav__link {
      padding: 0 2rem 1rem;
      border-bottom: 1px solid var(--color-primary-05);
      margin-bottom: 1rem;
      color: var(--color-primary);
      &:hover,
      &:focus-visible {
        color: var(--color-secondary);
      }
      &:active {
        color: var(--color-accent);
      }
    }
  }
}

.greedy-button {
  position: absolute;
  top: 1.5rem;
  right: 1.3rem;
  z-index: 1;
  display: none;
  justify-content: center;
  align-items: center;
  width: 1.8rem;
  height: 1.8rem;
  border: none;
  background-color: transparent;
  color: var(--color-white);
  transition: color var(--transition-medium-cubic);
  @include bp(xs) {
    right: 0.6rem;
  }
  &:hover,
  &:focus-visible {
    color: var(--color-secondary);
  }
  &:active {
    color: var(--color-accent);
  }

  .icon {
    font-size: 1.8rem;
  }

  &.is-visible {
    display: inline-flex;
  }

  & + .dropdown-list {
    top: calc(100% + 0.4rem);
    right: 0;
    left: initial;
    transform: none;
    animation: sublist-appear-short 0.2s cubic-bezier(0.33, 1, 0.68, 1);
  }
}

@keyframes sublist-appear-short {
  0% {
    top: 5rem;
    opacity: 0;
  }
}

/*! Sub header rules */

.sub-header {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.drug-index {
  --height: 3.8rem;

  overflow-y: hidden;
  height: var(--height);
  @include bp(sm) {
    width: calc(100% + 1.5rem);
  }
}

.drug-index__container {
  overflow-x: auto;
  overflow-y: hidden;
  // Hide scrollbar
  height: calc(var(--height) + 2rem);
}

.drug-index__list {
  display: flex;
  justify-content: space-between;
  gap: 0.5rem;
  padding-left: 0;
  margin-bottom: 0;
  list-style: none;
}

.drug-index__item {
  margin-bottom: 0;
  @include bp(sm) {
    &:last-child {
      padding-right: 1.5rem;
    }
  }
}

.drug-index__link {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 3.8rem;
  height: 3.8rem;
  border-radius: 1rem;
  font-family: var(--font-medium);
  text-decoration: none;
  background-color: var(--color-white);
  color: var(--color-primary);
  user-select: none;
  &:hover,
  &:focus-visible {
    color: var(--color-secondary);
  }
  &:active {
    color: var(--color-accent);
  }
}

.store-info {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  column-gap: 3rem;
  row-gap: 0.7rem;
  padding: 2rem;
  border-radius: 2rem;
  background-color: var(--color-white);
  @include bp(lg) {
    row-gap: 2rem;
  }
}

.store-info-caption {
  display: flex;
  flex-grow: 100;
  flex-direction: column;
  gap: 0.2rem;
  padding-left: 3.8rem;
  background-image: url('~SVG/ui/red-shield.svg');
  background-position: center left;
  background-repeat: no-repeat;
}

.store-info-caption__title {
  font-family: var(--font-bold);
  font-size: 1.5rem;
  line-height: 1.9rem;
}

.store-info-caption__text {
  font-size: 1.2rem;
  line-height: 1.5rem;
}

.store-info-blocks {
  display: flex;
  flex-grow: 1;
  flex-wrap: wrap;
  justify-content: space-between;
  column-gap: 0;
  row-gap: 1rem;
  @include bp(md) {
    justify-content: space-between;
    column-gap: 1rem;
  }
  @include bp(lg) {
    justify-content: space-around;
    gap: 3rem;
  }
}

.store-info-block {
  position: relative;
  display: flex;
  flex-shrink: 0;
  flex-basis: 100%;
  flex-direction: column;
  max-width: 15rem;
  font-size: 1.2rem;
  line-height: 1.5rem;
  @include bp(xs) {
    max-width: 13rem;
  }
  @include bp(md) {
    max-width: 14rem;
  }
  @include bp(lg) {
    flex-basis: initial;
    max-width: initial;
    padding-left: 3.2rem;
  }
  &::before {
    content: '';
    position: absolute;
    top: 0.9rem;
    left: 0;
    width: 1.8rem;
    height: 1.8rem;
    background-image: url('~SVG/ui/discount.svg');
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
    @include bp(lg) {
      top: 50%;
      left: 0;
      width: 2.2rem;
      height: 2.2rem;
      transform: translateY(-50%);
    }
  }

  &--2::before {
    background-image: url('~SVG/ui/document.svg');
  }
  &--3::before {
    background-image: url('~SVG/ui/box.svg');
  }
  &--4::before {
    background-image: url('~SVG/ui/shield.svg');
  }
}

.store-info-block__title {
  display: flex;
  align-items: center;
  min-height: 3rem;
  padding-left: 2.3rem;
  margin-top: 0.3rem;
  font-family: var(--font-bold);
  @include bp(lg) {
    min-height: initial;
    padding-left: 0;
  }
}
