/*! Typography settings */

@use 'sass:map';
@use 'sass:list';

//? Common weight name mapping
// 100 > Thin (Hairline)
// 200 > Extra Light (Ultra Light)
// 300 > Light
// 400 > Normal (Regular)
// 500 > Medium
// 600 > Semi Bold (Demi Bold)
// 700 > Bold
// 800 > Extra Bold (Ultra Bold)
// 900 > Black (Heavy)

$fonts: (
  'Plus Jakarta Sans Regular': (
    'filename': 'plus-jakarta-sans-regular',
    'style': normal,
    'weight': 400,
    'family': sans-serif,
    'root-var': '--font-regular',
  ),
  'Plus Jakarta Sans Medium': (
    'filename': 'plus-jakarta-sans-medium',
    'style': normal,
    'weight': 500,
    'family': sans-serif,
    'root-var': '--font-medium',
  ),
  'Plus Jakarta Sans Bold': (
    'filename': 'plus-jakarta-sans-bold',
    'style': normal,
    'weight': 700,
    'family': sans-serif,
    'root-var': '--font-bold',
  ),
);

$font-names: map.keys($fonts);

@each $font, $props in $fonts {
  $i: list.index(($fonts), ($font $props));
  $filename: map.get($props, filename);
  @font-face {
    font-family: list.nth($font-names, $i);
    src:
      url('~Fonts/#{$filename}.woff2') format('woff2'),
      url('~Fonts/#{$filename}.woff') format('woff');
    font-weight: map.get($props, weight);
    font-style: map.get($props, style);
    font-display: fallback;
  }
}

:root {
  @each $font, $props in $fonts {
    $i: list.index(($fonts), ($font $props));
    $root-var: map.get($props, root-var);
    $name: list.nth($font-names, $i);
    $family: map.get($props, family);

    #{$root-var}: $name, $family;
  }
  // Other font variables goes here
}

// Necessary class to include @font-face rules in critical CSS
.critical-css-font-carrier {
  @each $font, $props in $fonts {
    $i: list.index(($fonts), ($font $props));
    $name: list.nth($font-names, $i);

    font-family: $name;
  }
}
