/*! Product page */

@use '../abstracts/' as *;

.panel.product-panel {
  margin-bottom: 2rem;
  @include bp(md) {
    padding-bottom: 1rem;
  }
  @include bp(xl) {
    padding-bottom: 2rem;
  }

  & + .raw-content {
    margin-top: 3rem;
    @include bp(md) {
      margin-top: 4rem;
    }
  }
}

.table {
  table-layout: fixed;
  width: 100%;
  border-collapse: collapse;
  &:not(.cart-table) thead {
    @include bp(sm) {
      display: none;
    }
  }
  th {
    padding-bottom: 1rem;
    font-size: 1.3rem;
    font-weight: 400;
    line-height: 1.6rem;
    text-align: left;
  }
  td {
    @include bp(md) {
      border-top: 1px solid var(--color-primary-05);
    }
  }
}

.product {
  @include bp-max(430px) {
    grid-template-columns: 1.3fr 0.7fr;
    row-gap: 2rem;
  }
  @include bp(sm) {
    display: grid;
    grid-template-columns: 1fr 1fr;
    row-gap: 1rem;
    width: 100%;
    &:not(:last-child) {
      padding-bottom: 2rem;
      border-bottom: 1px solid var(--color-primary-40);
      margin-bottom: 2rem;
    }
  }
}

.product__info-wrapper {
  @include bp(sm) {
    &::before {
      content: attr(data-caption);
      display: block;
      margin-bottom: 0.5rem;
      color: var(--color-primary-50);
    }
  }
}

.product__info {
  display: flex;
  flex-direction: column;
  justify-content: center;
  @include bp(sm) {
    gap: 0.5rem;
  }
  @include bp(md) {
    min-height: 6.8rem;
    padding: 1rem 0 1rem;
  }

  &--sale {
    padding-left: 3.2rem;
    background-image: url('~SVG/ui/discount.svg');
    background-position: center left;
    background-repeat: no-repeat;
    @include bp(md) {
      padding-left: 3.2rem;
    }
  }
}

.product__quantity {
  font-family: var(--font-bold);
  font-size: 1.6rem;
}

.product__delivery {
  font-size: 1.3rem;
  line-height: 1.6rem;
}

.product__price-per-pill {
  @include bp-max(430px) {
    grid-column: 2;
    grid-row: 1;
    margin: auto;
  }
  @include bp(sm) {
    grid-column: 1;
    font-size: 1.6rem;
    &::before {
      content: attr(data-caption);
      display: block;
      margin-bottom: 0.5rem;
      font-size: 1.4rem;
      line-height: 2rem;
      color: var(--color-primary-50);
    }
  }
}

.product__price-wrapper {
  line-height: 1.8rem;
  @include bp-max(430px) {
    display: flex;
    grid-column: span 2;
    grid-row: 2;
    justify-content: space-between;
    align-items: center;
  }
  @include bp(sm) {
    grid-column: 1;
    font-size: 1.6rem;
    &::before {
      @include bp-max(430px) {
        margin-bottom: 0;
      }
      content: attr(data-caption);
      display: block;
      margin-bottom: 0.5rem;
      font-size: 1.4rem;
      line-height: 2rem;
      color: var(--color-primary-50);
    }
  }
}

.product__discount {
  color: var(--color-secondary);
  @include bp-max(430px) {
    margin-bottom: 0;
    font-size: 1.4rem;
  }
  @include bp(sm) {
    margin-bottom: 1rem;
  }
}

.product__price {
  @include bp(sm) {
    font-family: var(--font-bold);
  }
}

.product__button-wrapper {
  text-align: center;
  @include bp-max(430px) {
    grid-column: span 2;
    grid-row: 3;
  }
  @include bp(sm) {
    display: flex;
    justify-content: center;
    align-items: center;
    grid-column: 2;
    grid-row: 1 / span 3;
  }
}

.product__button {
  @include bp-max(430px) {
    width: 100%;
  }
  .button__text {
    flex-shrink: 0;
    @include bp-min(1040px) {
      display: none;
    }
    @include bp(xl) {
      display: initial;
    }
  }
}
