/*! Pop-ups & modal dialogs */

@use '../abstracts' as *;

.dialog {
  min-width: 32rem;
  width: 100%;
  max-width: 70.8rem;
  height: auto;
  padding: 2rem;
  border: none;
  border-radius: 2rem;
  margin: auto;
  background-color: transparent;
  @include bp(sm) {
    padding: 1rem;
  }
  &[data-modal='false'] {
    margin-top: 10rem;
  }
}

.dialog[open] {
  animation: dialogSlideInUp 0.25s cubic-bezier(0.71, 1.7, 0.77, 1.24) forwards;
}

.dialog.close {
  animation: dialogSlideOutDown 0.25s ease backwards;
}

@keyframes dialogSlideInUp {
  from {
    opacity: 0;
    transform: translate3d(0, 4rem, 0);
  }
  to {
    opacity: 1;
    transform: translate3d(0, 0, 0);
  }
}

@keyframes dialogSlideOutDown {
  from {
    opacity: 1;
    transform: translate3d(0, 0, 0);
  }
  to {
    opacity: 0;
    transform: translate3d(0, 8rem, 0);
  }
}

.dialog[open]::backdrop {
  // Firefox
  background-color: var(--color-overlay-bg);
  animation: backdropFadeIn 0.3s ease forwards;
}

.dialog.close::backdrop {
  animation: backdropFadeOut 0.3s ease backwards;
}

@keyframes backdropFadeIn {
  from {
    background-color: transparent;
  }
  to {
    background-color: var(--color-overlay-bg);
  }
}

@keyframes backdropFadeOut {
  from {
    background-color: var(--color-overlay-bg);
  }
  to {
    background-color: transparent;
  }
}

.dialog__inner-wrapper {
  position: relative;
  top: 50%;
  z-index: 9999;
  padding: 3rem;
  border-radius: 2rem;
  background-color: var(--color-white);
  box-shadow: var(--shadow-dialog);
  transform: translateY(-50%);
}

.dialog__close-button {
  position: absolute;
  top: 1.2rem;
  right: 1.2rem;
}

.dialog__header {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.5rem;
  margin-bottom: 2rem;
}

.dialog__title {
  font-family: var(--font-bold);
  font-size: 3rem;
  line-height: 3.8rem;
}

.dialog__note {
  font-size: 1.6rem;
  text-align: center;
}

/* Callback form */

.callback-form,
.callback-push-form {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
  max-width: 30rem;
  margin: 0 auto;
  .form__field {
    width: 100%;
  }
  .form__submit {
    margin: 0 auto;
  }
}

.callback-push-form {
  .form__submit {
    margin: initial;
  }
}

.callback-push-submit {
  flex-direction: row;
  justify-content: center;
  gap: 1rem;
  .button {
    flex: 1 0 0;
    max-width: 9.2rem;
  }
}
