/*! General layout structure */

@use '../abstracts' as *;

body {
  display: flex;
  flex-direction: column;
}

.page-wrapper {
  display: flex;
  flex: 1 0 auto;
  gap: 2rem;
  padding-top: 2rem;
  padding-bottom: 5rem;
  @include bp(md) {
    padding-top: 3rem;
    padding-bottom: 6rem;
  }
}

.main {
  flex-grow: 1;
}

.main--grid {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  @include bp(md) {
    display: grid;
    grid-template-columns: minmax(auto, 62rem) minmax(22rem, auto);
    grid-auto-rows: min-content;
    row-gap: 0;
  }

  h1,
  .heading-wrapper {
    grid-column: span 2;
  }
}

.main--grid.main--aside-xl {
  display: flex;
  flex-direction: column;
  row-gap: 2rem;
  @include bp(sm) {
    row-gap: 1rem;
  }
  @include bp-min(1040px) {
    display: grid;
    grid-template-columns: minmax(40rem, 68rem) minmax(22rem, auto);
    row-gap: 0;
  }
  .main__aside {
    max-width: initial;
    margin-top: 1rem;
    @include bp(md) {
      margin-top: 2rem;
    }
    @include bp-min(1040px) {
      max-width: 30rem;
      margin-top: 0;
    }
    &--narrow {
      @include bp-min(1040px) {
        max-width: 24rem;
      }
    }
  }
}

.main--grid.main--aside-124 {
  @include bp-min(1040px) {
    display: flex;
  }
  @include bp-min(1240px) {
    display: grid;
    grid-template-columns: minmax(40rem, 68rem) minmax(22rem, auto);
    row-gap: 0;
  }
  .main__aside {
    @include bp-min(1040px) {
      max-width: initial;
      margin-top: 4rem;
    }
    @include bp-min(1240px) {
      margin-top: 0;
    }
    &--narrow {
      @include bp-min(1240px) {
        max-width: 24rem;
      }
    }
  }
}

.main__aside {
  align-self: flex-start;
  width: 100%;
  max-width: 30rem;

  &--narrow {
    max-width: 24rem;
  }
}

.heading-wrapper {
  display: grid;
  grid-template-columns: minmax(36rem, 68rem) minmax(22rem, auto);
  gap: 2rem;
  @include bp(sm) {
    display: block;
  }

  h1 {
    grid-column: 1;
  }

  .button {
    align-self: flex-start;
    @include bp(sm) {
      display: none;
    }
    .icon {
      font-size: 1.5rem;
      filter: invert(100%);
      transform: rotate(-180deg);
    }
  }
}

.product-page {
  .main--grid.main--aside-xl {
    h1 {
      order: -1;
      @include bp-min(1040px) {
        order: initial;
      }
    }
    .main__aside {
      order: -1;
      @include bp(md) {
        margin-top: -2rem;
      }
      @include bp-min(1040px) {
        order: initial;
        margin-top: 0;
      }
    }
  }
}
