/*! Aside navigation rules */

@use '../abstracts' as *;

.aside {
  display: none;
  flex-shrink: 0;
  order: -1;
  width: 24rem;
  @include bp(lg) {
    display: block;
  }
}

/* Aside navigation */

.aside-nav {
  border-radius: 2rem;
  background-color: var(--color-primary);

  .accordion-item {
    &:first-child .accordion-button {
      border-radius: 2rem 2rem 0 0;
    }
    &:last-child {
      .accordion-button {
        min-height: 4.8rem;
        border-color: transparent;
        border-radius: 0 0 2rem 2rem;
        &[aria-expanded='true'] {
          border-color: var(--color-white-10);
        }
      }
      .aside-nav__list {
        margin-bottom: 1.6rem;
      }
    }
  }

  .accordion-button {
    display: flex;
    align-items: center;
    width: 100%;
    min-height: 4.9rem;
    padding-left: 4.2rem;
    border: none;
    border-bottom: 1px solid var(--color-white-10);
    font-family: var(--font-bold);
    font-size: 1.4rem;
    line-height: 1.8rem;
    background-color: transparent;
    color: var(--color-white);
    transition:
      color var(--transition-medium-cubic),
      border-color var(--transition-medium-cubic);
    &:hover,
    &:focus-visible {
      color: var(--color-secondary);
      &::before,
      &::after {
        background-color: var(--color-secondary);
      }
    }
    &:active {
      color: var(--color-accent);
      &::before,
      &::after {
        background-color: var(--color-accent);
      }
    }

    &::before,
    &::after {
      content: '';
      position: absolute;
      top: 2.3rem;
      left: 2rem;
      width: 1.2rem;
      height: 0.2rem;
      border-radius: 1rem;
      background-color: var(--color-white);
      transition:
        transform var(--transition-medium-cubic),
        background-color var(--transition-medium-cubic);
    }
    &::after {
      top: 1.8rem;
      left: 2.5rem;
      width: 0.2rem;
      height: 1.2rem;
    }
    &[aria-expanded='true'] {
      &::before {
        background-color: var(--color-secondary);
      }
      &::after {
        background-color: var(--color-secondary);
        transform: scaleY(0);
      }
    }
  }
}

.aside-nav__list {
  display: flex;
  flex-direction: column;
  gap: 0.8rem;
  margin-top: 1.5rem;
  margin-bottom: 0.8rem;
}

.aside-nav__item {
  padding-right: 2rem;
  margin-bottom: 0;
}

.aside-nav__link {
  display: inline-flex;
  justify-content: space-between;
  width: 100%;
  font-size: 1.4rem;
  line-height: 1.8rem;
  text-decoration: none;
  color: var(--color-white);
  &:hover,
  &:focus-visible {
    color: var(--color-secondary);
  }
  &:active {
    color: var(--color-accent);
  }
}

.aside-nav__price {
  color: var(--color-secondary);
}

/* Aside promo block */

.aside-promo {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  margin-top: 2rem;
  margin-bottom: -0.1rem;
  .combo-card {
    background-position: center right 2rem;
  }
  .combo-card__link-wrapper {
    gap: 1rem;
    padding: 2rem;
  }
  .combo-card__title {
    margin: 0;
    font-size: 2rem;
    line-height: 2.4rem;
  }
  .combo-card__text {
    font-size: 1.3rem;
    line-height: 1.6rem;
  }
}
