/*! Main & sup footer rules */

@use '../abstracts' as *;

/* Subscribe block */

.subscribe {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 2rem;
  padding: 2rem;
  border-radius: 2rem;
  background-color: var(--color-primary);
  @include bp-min(800px) {
    flex-direction: row;
    padding: 3rem;
  }
}

.subscribe__caption {
  display: flex;
  flex-direction: column;
  height: 4.1rem;
  padding-left: 5rem;
  background-image: url('~SVG/ui/envelope-open.svg');
  background-position: center left;
  background-repeat: no-repeat;
  background-size: 3rem;
  @include bp-min(800px) {
    height: initial;
    padding-left: 6rem;
    background-size: initial;
  }
}

.subscribe__title {
  font-family: var(--font-bold);
  font-size: 1.8rem;
  line-height: 2.3rem;
  color: var(--color-white);
  @include bp-min(800px) {
    font-size: 2.2rem;
    line-height: 2.8rem;
  }
}

.subscribe__text {
  font-size: 1.4rem;
  line-height: 1.8rem;
  color: var(--color-secondary);
  @include bp-min(800px) {
    font-size: 1.6rem;
    line-height: 2rem;
  }
}

/* Footer testimonials */

.footer-testimonials {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(30rem, 1fr));
  gap: 1rem;
  margin: 2rem 0;
  @include bp-max(359.98px) {
    grid-template-columns: repeat(auto-fit, minmax(29rem, 1fr));
  }
  @include bp(md) {
    gap: 2rem;
    margin: 6rem 0;
  }
  @include bp(lg) {
    grid-template-columns: repeat(auto-fit, minmax(40rem, 1fr));
  }
  @include bp(xl) {
    grid-template-columns: repeat(auto-fit, minmax(26rem, 1fr));
  }
}

/* Main footer ruler */

.footer {
  padding: 4rem 0 10.6rem;
  border-radius: 4rem 4rem 0 0;
  background-color: var(--color-primary);
  @include bp(md) {
    padding: 4rem 0;
  }

  .container {
    display: flex;
    flex-direction: column;
    gap: 2.8rem;
  }
}

.footer__wrapper {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  gap: 2rem;
  @include bp(md) {
    gap: 3rem;
  }
  @include bp-min(1000px) {
    flex-direction: row;
    gap: 2rem;
  }
}

.logo.logo--footer {
  column-gap: 1rem;
  height: 4.8rem;
  padding: 0.2rem 0 0;
  margin-top: 0;
  color: var(--color-white);
  @include bp(sm) {
    height: 4.4rem;
  }
  @include bp(lg) {
    margin-bottom: 0;
  }

  &:hover,
  &:focus-visible {
    color: var(--color-white-75);
  }
  &:active {
    color: var(--color-white);
  }

  .logo__image {
    width: 4rem;
    height: 4rem;
    @include bp(sm) {
      position: relative;
      top: -0.2rem;
    }
  }
  .logo__title {
    font-size: 2rem;
    line-height: 2.5rem;
    @include bp(sm) {
      margin-bottom: 0.4rem;
      line-height: 2.1rem;
    }
  }
  .logo__text {
    font-size: 1.3rem;
    line-height: 1.6rem;
  }
}

.footer-nav {
  flex-grow: 1;
  max-width: 71rem;
  line-height: 1.8rem;

  .nav__list {
    flex-direction: column;
    justify-content: space-between;
    gap: 2rem;
    @include bp(md) {
      flex-direction: row;
      gap: 1rem;
    }
  }

  .nav__link {
    font-family: var(--font-bold);
    color: var(--color-white);
    &:hover,
    &:focus-visible {
      color: var(--color-secondary);
    }
    &:active {
      color: var(--color-accent);
    }
  }
}

.footer__copyrights {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.2rem;
  font-size: 1.2rem;
  color: var(--color-white-75);
  @include bp(sm) {
    text-align: center;
  }
}

/* Footer controls */

.footer-buttons {
  position: fixed;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 101;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 6.8rem;
  border-radius: 2rem 2rem 0 0;
  background-color: var(--color-secondary);
  @include bp(md) {
    display: none;
  }

  &__container {
    display: flex;
    width: 100%;
    max-width: 43rem;
  }
}

.footer-button {
  position: relative;
  display: flex;
  flex: 1 0 0;
  flex-direction: column;
  align-items: center;
  gap: 0.2rem;
  border: none;
  font-family: var(--font-bold);
  font-size: 1.3rem;
  line-height: 1.6rem;
  text-decoration: none;
  background-color: transparent;
  color: var(--color-white);
  transition: color var(--transition-medium-cubic);
  &:hover,
  &:focus-visible {
    color: var(--color-white-75);
  }

  .icon {
    font-size: 2.4rem;
  }

  .button__text {
    top: 0;
    font-size: inherit;
  }

  &--cart::after {
    top: -0.6rem;
    right: 3.1rem;
    left: initial;
    background-color: var(--color-primary);
  }
}
