/*! Categories nav */

@use '../abstracts' as *;

.cat-nav {
  position: absolute;
  top: 11.2rem;
  z-index: 100;
  display: none;
  width: calc(100% - 4rem);
  max-height: calc(80vh - 6rem);
  border-radius: 0 0 2rem 2rem;
  background-color: var(--color-light-blue);
  @include bp(sm) {
    position: fixed;
    top: 0;
    right: 0;
    display: flex;
    width: 100%;
    max-width: 39rem;
    height: 100%;
    max-height: initial;
    border-radius: 0;
    margin-left: -1.5rem;
    background-color: var(--color-white);
    transform: translateX(100%);
    transition: transform var(--transition-long-cubic);
  }
  @include bp(md) {
    top: 14rem;
  }
  @include bp(lg) {
    top: 15rem;
  }
  @include bp(xl) {
    top: 11.2rem;
  }

  &.is-visible {
    display: flex;
    @include bp(sm) {
      transform: translateX(0%);
      transition: transform var(--transition-long-cubic);
    }
  }

  .nav-container {
    position: relative;
    display: flex;
    flex-grow: 1;
    @include bp(sm) {
      flex-direction: column;
      padding: 2rem 2rem 6.8rem;
    }
  }

  .nav__heading {
    padding-bottom: 0.9rem;
    font-family: var(--font-bold);
    font-size: 2rem;
    line-height: 2.5rem;
    @include bp(md) {
      display: none;
    }
  }

  .nav__list {
    overflow: auto;
    flex-direction: column;
    align-items: initial;
    gap: 0;
    width: 24rem;
    max-height: 74rem;
    @include bp(sm) {
      width: 100%;
      height: 100%;
      max-height: initial;
    }
    @include bp(md) {
      border-bottom-left-radius: 2rem;
    }

    & > .nav__item {
      border-bottom: 1px solid var(--color-primary-10);
      background-color: var(--color-white);
      &:last-child {
        @include bp(md) {
          border: none;
          border-bottom-left-radius: 2rem;
        }
      }
      & > .nav__link {
        padding: 1rem 2rem 0.8rem;
        font-family: var(--font-bold);
        @include bp(sm) {
          padding: 1rem 0 0.8rem;
        }
      }
    }
  }

  .nav__link {
    color: var(--color-primary);
    &.is-active {
      @include bp(md) {
        color: var(--color-secondary);
      }
    }
    &:hover,
    &:focus-visible {
      color: var(--color-secondary);
    }
    &:active {
      color: var(--color-accent);
    }
  }

  .nav__sublist {
    position: absolute;
    top: 0;
    visibility: hidden;
    overflow: auto;
    flex-wrap: wrap;
    padding: 2rem;
    margin: 0;
    background-color: transparent;
    box-shadow: none;
    @include bp(sm) {
      left: 0;
      visibility: visible;
      flex-direction: column;
      flex-wrap: nowrap;
      justify-content: initial;
      gap: 1.5rem;
      height: 100%;
      max-height: 100%;
      background-color: var(--color-white);
      transform: translateX(100%);
      transition: transform var(--transition-long-cubic);
    }
    @include bp(md) {
      max-height: min(72rem, calc(80vh - 8rem));
      padding: 3rem;
    }
    &.is-visible {
      visibility: visible;
      @include bp(sm) {
        transform: translateX(0%);
      }
    }
    .nav__link {
      font-size: 1.4rem;
      line-height: 1.8rem;
      @include bp(md) {
        font-size: 1.5rem;
        line-height: 1.9rem;
      }
    }
  }

  .sublist-4-col {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    column-gap: 4rem;
    width: calc(100% - 24rem);
    @include bp(sm) {
      width: 100%;
    }
  }

  .nav__item--return {
    margin-bottom: 0.5rem;
    @include bp(md) {
      display: none;
    }
  }

  .nav__mobile-return {
    padding-left: 2.3rem;
    border: none;
    font-family: var(--font-bold);
    font-size: 2rem;
    line-height: 2.5rem;
    background-image: url('~SVG/ui/back.svg');
    background-position: center left;
    background-repeat: no-repeat;
    background-color: transparent;
    transition: color var(--transition-medium-cubic);
    &:hover,
    &:focus-visible {
      color: var(--color-primary-75);
    }
    &:active {
      color: var(--color-primary-50);
    }
  }

  .nav-col {
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
    @include bp(md) {
      flex-basis: calc(50% - 2rem);
      gap: 2.4rem;
    }
    @include bp(lg) {
      flex-basis: initial;
    }
  }

  .nav__close-button {
    position: absolute;
    top: 1rem;
    right: 1rem;
    z-index: 1;
    width: 2.4rem;
    height: 2.4rem;
    border: none;
    background-image: url('~SVG/ui/close.svg');
    background-position: center;
    background-repeat: no-repeat;
    background-color: transparent;
    opacity: 0.25;
    transition: opacity var(--transition-medium-cubic);
    @include bp(md) {
      display: none;
    }

    &:hover,
    &:focus-visible {
      opacity: 0.4;
    }
    &:active {
      opacity: 0.5;
    }
  }
}

.cat-overlay {
  @include bp(md) {
    top: 15.5rem;
    transition: none;
  }
  @include bp(lg) {
    top: 16.5rem;
  }
  @include bp(xl) {
    top: 12.7rem;
  }
  &.is-visible {
    transition: var(--transition-overlay);
  }
}

body.is-fixed {
  position: fixed;
  width: 100%;
}

body.has-scroll {
  overflow-y: scroll;
  // Firefox
  overflow-x: unset;
}
