/*! General reusable utility classes */

@use '../abstracts' as *;

// Visually hidden, accessible, doesn't take up space in the DOM
.offscreen {
  position: absolute;
  overflow: hidden;
  width: 1px;
  height: 1px;
  padding: 0;
  border: 0;
  margin: -1px;
  clip: rect(0 0 0 0);
}

$flex-direction:
  [ row,
  row-reverse,
  column,
  column-reverse,
  ];

$justify-content: (
  flex-start: start,
  flex-end: end,
  center: center,
  space-between: between,
  space-around: around,
);

$align-items: (
  center: center,
);

@mixin flex-direction-generator($flex-direction, $viewport: null) {
  @if $viewport {
    $viewport: '-' + $viewport;
  }
  @each $value in $flex-direction {
    .flex#{$viewport}-#{$value} {
      flex-direction: $value;
    }
  }
}

@mixin justify-generator($justify-content, $viewport: null) {
  @if $viewport {
    $viewport: '-' + $viewport;
  }
  @each $value, $alias in $justify-content {
    .justify#{$viewport}-#{$alias} {
      justify-content: $value;
    }
  }
}

@mixin align-generator($align-items, $viewport: null) {
  @if $viewport {
    $viewport: '-' + $viewport;
  }
  @each $value, $alias in $align-items {
    .align#{$viewport}-#{$alias} {
      align-items: $value;
    }
  }
}

@if $grid {
  /*! Bootstrap-like layout building utilities */

  .flex {
    display: flex;
  }

  // Flex-direction utility classes
  @include flex-direction-generator($flex-direction);

  // Justify content utility classes
  @include justify-generator($justify-content);

  // Align items utility classes
  @include align-generator($align-items);

  // Responsive utility classes
  @each $viewport, $size in $viewports {
    @include bp($viewport) {
      @include flex-direction-generator($flex-direction, $viewport);
      @include justify-generator($justify-content, $viewport);
      @include align-generator($align-items, $viewport);
    }
  }

  .overflow-hidden {
    overflow: hidden;
  }
}
