/*! Forms and their elements */

@use '../abstracts' as *;

.form--flex {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  @include bp(md) {
    gap: 2rem;
  }
}

.form-panel {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
  padding: 2rem;
  border-radius: 2rem;
  background-color: var(--color-white);
  @include bp(lg) {
    padding: 3rem;
  }
}

.form__fieldset.form-panel--border-legend {
  legend {
    padding-bottom: 1.5rem;
    border-bottom: 1px solid var(--color-primary-05);
    margin-bottom: 0;
  }
}

.form__fieldset {
  border: 0;
  &--flex {
    display: flex;
    flex-wrap: wrap;
    gap: 1.6rem;
    .form__field {
      flex: 1 0 auto;
      min-width: 28rem;
      @include bp-max(359.98px) {
        min-width: 25rem;
      }
    }
  }

  legend {
    // Return element into normal flow
    float: left;
    margin-bottom: 0.5rem;
    font-family: var(--font-bold);
    font-size: 2rem;
    line-height: 2.5rem;
  }

  &--2-col {
    display: flex;
    flex-wrap: wrap;
    gap: 1.6rem;
    @include bp-min(575px) {
      padding-bottom: 1.5rem;
      border-bottom: 1px solid var(--color-primary-05);
    }
    & > .form__field {
      flex: 1 0 0;
      min-width: 22rem;
    }
  }

  &--last-child {
    padding-bottom: 0;
    border: none;
    @include bp-min(575px) {
      padding-bottom: 0;
      border: none;
    }
  }
}

.form__footnote {
  margin-bottom: 0;
  font-size: 12px;
  line-height: 15px;
}

.form__field {
  position: relative;
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  &:last-child {
    margin-bottom: 0;
  }

  &--border {
    padding-bottom: 1.5rem;
    border-bottom: 1px solid var(--color-primary-05);
    margin-bottom: 0;
  }
}

.form__field--checkbox {
  flex-direction: row;
  align-items: center;
  min-height: 2rem;

  .form__label--checkbox {
    width: initial;
    font-size: 1.6rem;
    line-height: 2rem;
  }
}

.form__label,
.form-label {
  display: inline-block;
  order: -1;
  width: 100%;
  font-size: 1.2rem;
  line-height: 1.5rem;
}

.form__text-input {
  width: 100%;
  min-height: 4.8rem;
  padding: 1rem 5.8rem 1rem 2rem;
  border: 1px solid var(--color-primary-20);
  border-radius: 1rem;
  &:focus-visible {
    &::placeholder {
      color: transparent;
    }
  }
  &::placeholder {
    color: var(--color-primary-50);
  }
}

.input-textarea {
  min-height: 9.6rem;
  max-height: 20rem;
  resize: vertical;
}

.input__file {
  opacity: 0;
}

.checkbox-wrapper {
  display: flex;
  align-items: center;
  min-height: 6.4rem;
}

.form__checkbox {
  position: absolute;
  opacity: 0;
  &:checked + .form__label--checkbox::after {
    display: flex;
  }
}

.form__label--checkbox,
.form-radio {
  &:hover,
  &:focus-visible {
    &::before {
      border-color: var(--color-primary-75);
    }
  }
  &:active {
    &::before {
      background-color: var(--color-primary-05);
    }
  }
  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1;
    width: 2rem;
    height: 2rem;
    border: 1px solid var(--color-primary-20);
    border-radius: 50%;
    background-color: transparent;
    transition:
      border-color var(--transition-medium-cubic),
      background-color var(--transition-medium-cubic);
  }
  &::after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    display: none;
    flex-shrink: 0;
    width: 2rem;
    height: 2rem;
    border-radius: 50%;
    background-image: url('~SVG/ui/check.svg');
    background-position: center;
    background-repeat: no-repeat;
    background-color: var(--color-primary);
    transition: background-color var(--transition-medium-cubic);
  }
}

.form__label--checkbox {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-left: 3rem;
  user-select: none;
  cursor: pointer;
  &::before,
  &::after {
    top: 50%;
    transform: translateY(-50%);
  }
}

.form__label-title {
  margin-bottom: 0.2rem;
  font-family: var(--font-bold);
  font-size: 1.3rem;
  line-height: 1.6rem;
}

.form__label--checkbox a {
  display: inline;
}

.form__submit {
  gap: 1rem;
  width: fit-content;

  .icon {
    font-size: 1.5rem;
  }

  &--wide {
    width: 100%;
  }
}

.select-wrapper {
  position: relative;
  width: 100%;

  &__chevron {
    position: absolute;
    top: 1.9rem;
    right: 2.1rem;
    font-size: 1rem;
    transition:
      transform var(--transition-medium-cubic),
      color var(--transition-medium-cubic);
  }
}

.custom-field {
  min-height: 6.4rem;
}

.captcha {
  aspect-ratio: 29.1 / 7.4;
  max-width: 29.1rem;
  @include bg-picture('captcha.jpg', 582);
  background-size: contain;
}

.form__caption {
  font-size: 1.4rem;
  line-height: 2.2rem;
  p {
    margin-bottom: 1rem;
    &:last-child {
      margin-bottom: 0;
    }
  }
}

.qty-input {
  display: flex;
  align-items: center;
}

.qty-input__qty-field {
  width: 100%;
  border: none;
  font-size: 1.6rem;
  text-align: center;
}

.qty-input__minus,
.qty-input__plus {
  flex-shrink: 0;
  width: 2.4rem;
  height: 2.4rem;
  border: none;
  background-image: url('~SVG/ui/minus.svg');
  background-position: center;
  background-repeat: no-repeat;
  background-color: transparent;
  color: transparent;
  transition: opacity var(--transition-medium-cubic);
  user-select: none;
}

.qty-input__plus {
  background-image: url('~SVG/ui/plus.svg');
}

.form-radio-wrapper {
  position: relative;
  width: 100%;
}

.form-radio-input {
  position: absolute;
  visibility: hidden;
  &:checked + .form-radio::after {
    display: flex;
  }
}

.form-radio {
  position: relative;
  display: flex;
  flex-direction: column;
  padding-left: 3rem;
  cursor: pointer;
}

.form-radio__title {
  margin-bottom: 0.2rem;
  font-family: var(--font-bold);
  font-size: 1.3rem;
  line-height: 1.6rem;
}

.form-radio__text {
  font-size: 1.2rem;
  line-height: 1.5rem;
}

.form-radio__price {
  position: absolute;
  top: 0;
  right: 0;
  font-size: 1.2rem;
  line-height: 1.5rem;
  color: var(--color-secondary);
}

/* Borderless form */

.form--secondary {
  .form__text-input {
    border: none;
  }
}

/* Search form */

.search-form {
  position: relative;
  display: flex;
  width: 100%;
  @include bp(xl) {
    max-width: 45.3rem;
  }

  .form__label {
    &:focus-within + .search-button {
      color: var(--color-primary);
    }
  }
}

.search-button {
  position: absolute;
  top: 50%;
  right: 2rem;
  width: 1.8rem;
  height: 1.8rem;
  border: none;
  background-color: transparent;
  color: var(--color-secondary);
  transform: translateY(-50%);
  transition: color var(--transition-medium-cubic);

  .icon {
    font-size: 1.8rem;
  }
}

/* Subscribe form */

.subscribe-form {
  display: flex;
  flex-grow: 1;
  gap: 1rem;
  @include bp-min(800px) {
    max-width: 42rem;
  }

  .form__field {
    margin-bottom: 0;
    &:first-child {
      flex-grow: 1;
    }
  }

  .form__submit {
    @include bp-max(499.98px) {
      overflow: hidden;
      width: 4.8rem;
      background-image: url('~SVG/ui/arrow.svg');
      background-position: center;
      background-repeat: no-repeat;
      color: transparent;
    }
  }
}
