//** Mixins to generate background images' routine rules

// Preferred usage is:
//? @include bg-picture('image.jpg', 1280);
//? @include bg-picture('body-bg.jpg', 1280, 'webp', 'body');

@use './helpers' as *;
@use 'sass:math';

@mixin bg-picture-ruleset($image, $width, $format: null) {
  @if $format {
    $format: 'format=#{$format}';
  }
  background-image: url('~Images/#{$image}?#{$format}&size=#{$width}');
  background-image: image-set(
    url('~Images/#{$image}?#{$format}&size=#{$width}') 1x,
    url('~Images/#{$image}?#{$format}') 2x
  );
}

@mixin bg-picture($image, $original-width, $format: 'webp', $body: null) {
  $width: math.div($original-width, 2);

  @include bg-picture-ruleset($image, $width);

  @if $body {
    &.webp {
      @include bg-picture-ruleset($image, $width, $format);
    }
  } @else {
    @include add-before-parent-selector('.#{$format} ') {
      @include bg-picture-ruleset($image, $width, $format);
    }
  }
}
