/*! Buttons system */

@use '../abstracts' as *;

.button {
  --bg-color: var(--color-secondary);
  --bg-hover-color: var(--color-accent);
  --bg-active-color: var(--color-secondary);
  --color: var(--color-white);

  display: inline-flex;
  justify-content: center;
  align-items: center;
  gap: 1.1rem;
  min-height: 4.8rem;
  padding: 0.5rem 2rem;
  border: none;
  border-radius: 1rem;
  font-family: var(--font-bold);
  font-size: 1.4rem;
  line-height: 1.8rem;
  text-decoration: none;
  text-align: center;
  background-color: var(--bg-color);
  color: var(--color);
  transition-property: transform, opacity, var(--transition-props-color);
  transition-timing-function: var(--transition-func-cubic);
  transition-duration: var(--transition-duration-medium);
  cursor: pointer;
  &:hover,
  &:focus-visible {
    background-color: var(--bg-hover-color);
    color: var(--color);
  }
  &:active {
    background-color: var(--bg-active-color);
    color: var(--color);
  }

  &__text {
    position: relative;
    top: -0.1rem;
  }
}

.button--secondary {
  --bg-color: var(--color-primary);
  --bg-hover-color: var(--color-primary-90);
  --bg-active-color: var(--color-primary);
}

.button--outline {
  --bg-color: transparent;
  --bg-hover-color: transparent;
  --bg-active-color: transparent;
  --color: var(--color-primary);

  border: 1px solid var(--color-secondary);
  &:hover,
  &:focus-visible {
    border-color: var(--color-primary);
  }
  &:active {
    border-color: var(--color-secondary);
  }
}

.icon-button {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  width: 1.6rem;
  height: 1.6rem;
  border: none;
  background-color: transparent;
  color: var(--color-secondary);
  transition: color var(--transition-medium-cubic);
  &:hover,
  &:focus-visible {
    color: var(--color-primary);
  }
  &:active {
    color: var(--color-secondary);
  }

  .icon {
    font-size: 1.6rem;
  }
}

.close-button {
  overflow: hidden;
  width: 2rem;
  height: 2rem;
  border: 0;
  background-color: transparent;
  color: transparent;
  opacity: 0.25;
  transition: opacity var(--transition-medium-cubic);
  user-select: none;
  &:hover {
    opacity: 0.5;
  }
  &::before,
  &::after {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    display: block;
    width: 0.2rem;
    height: 100%;
    border-radius: 0.6rem;
    margin: 0 auto;
    background-color: var(--color-black);
    transform: rotate(45deg) translateX(0);
  }
  &::after {
    transform: rotate(-45deg) translateX(0);
  }
}

.copy-button {
  position: relative;
  display: flex;
  flex-shrink: 0;
  width: 2.8rem;
  height: 2.8rem;
  border: none;
  border-radius: 0.6rem;
  background-image: url('~SVG/ui/copy.svg');
  background-position: center;
  background-repeat: no-repeat;
  background-color: transparent;
  transition: background-color var(--transition-medium-cubic);
  &:hover,
  &:focus-visible {
    background-color: var(--color-primary-20);
  }
  &:active {
    background-color: var(--color-primary-10);
  }

  .button-text {
    position: absolute;
    top: -4.7rem;
    left: -4rem;
    display: none;
    align-items: center;
    min-height: 3.9rem;
    padding: 0.5rem 2rem 0.5rem 4rem;
    border-radius: 1rem;
    font-family: var(--font-bold);
    font-size: 1.2rem;
    line-height: 1.5rem;
    background-image: url('~SVG/ui/check.svg');
    background-position: center left 2rem;
    background-repeat: no-repeat;
    background-color: var(--color-primary);
    color: var(--color-white);
    animation: sublist-appear 0.2s cubic-bezier(0.33, 1, 0.68, 1);
    user-select: none;
    &::after {
      content: '';
      position: absolute;
      bottom: -0.7rem;
      left: calc(50% - 0.6rem);
      width: 0;
      height: 0;
      border-width: 0.7rem 0.6rem 0 0.6rem;
      border-style: solid;
      border-color: var(--color-primary) transparent transparent transparent;
      transform: rotate(0deg);
    }
  }

  .button-text.is-visible {
    display: flex;
  }
}

@keyframes tooltip-appear {
  0% {
    opacity: 0;
  }
}
