/*! Cart & order pages */

@use '../abstracts/' as *;

/* Cart page */

.cart-table {
  thead {
    @include bp-max(574.98px) {
      display: none;
    }
  }
  th:nth-child(2) {
    padding-right: 2rem;
    text-align: center;
  }
  tbody tr:last-child .cart-item-wrapper {
    padding-bottom: 0;
  }
  td {
    padding-top: 1rem;
    @include bp-max(574.98px) {
      padding-top: 0;
    }
  }
  .cart-item__caption {
    padding-top: 0.8rem;
    @include bp-max(574.98px) {
      padding-top: 1.4rem;
    }
    @include bp(md) {
      padding-top: 0.8rem;
      border: none;
    }
  }
}

.cart-item-wrapper {
  padding-bottom: 1rem;
  @include bp(sm) {
    border-top: 1px solid var(--color-primary-05);
  }
}

.cart-item {
  table-layout: fixed;
  width: 100%;
  border-collapse: collapse;
  @include bp-max(574.98px) {
    tbody {
      display: flex;
      flex-direction: column;
    }
  }

  td {
    padding-top: 0;
    border: none;
    @include bp(md) {
      border: none;
    }
  }
  .discount-price {
    display: block;
    color: var(--color-secondary);
  }
}

.cart-item-content {
  @include bp-max(574.98px) {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
}

.cart-item__brand {
  font-family: var(--font-bold);
  font-size: 1.6rem;
  line-height: 2rem;
  @include bp-max(574.98px) {
    width: 100%;
  }
}

.cart-item__brand-wrapper {
  display: flex;
  align-items: center;
  min-height: 3.8rem;
  padding: 0.5rem 0;
}

.cart-item__qty {
  @include bp-max(574.98px) {
    width: initial;
    &::before {
      content: attr(data-caption);
      display: block;
      margin-bottom: 0.5rem;
      color: var(--color-primary-50);
    }
  }
}

.cart-item__pack-price {
  line-height: 1.8rem;
  @include bp-max(574.98px) {
    width: initial;
    &::before {
      content: attr(data-caption);
      display: block;
      margin-bottom: 0.5rem;
      color: var(--color-primary-50);
    }
  }
}

.cart-item__total-price {
  line-height: 1.8rem;
  @include bp-max(574.98px) {
    width: initial;
    &::before {
      content: attr(data-caption);
      display: block;
      margin-bottom: 0.5rem;
      color: var(--color-primary-50);
    }
  }
}

.cart-item__remove {
  @include bp-max(574.98px) {
    align-self: center;
    width: initial;
  }
}

.cart-item__caption {
  font-size: 1.1rem;
  line-height: 1.4rem;
  color: var(--color-secondary);
}

.pack-radios {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(18rem, 1fr));
  column-gap: 2rem;
  row-gap: 2rem;

  .form-radio-wrapper:first-child .form-radio__title {
    display: flex;
    align-items: center;
    min-height: 2rem;
  }
}

.discount-panel .form__field {
  // max-width: 62rem;
}

.discount-code {
  display: flex;
  align-items: center;
  gap: 2rem;
  @include bp(sm) {
    flex-wrap: wrap;
    gap: 1rem;
    .button {
      margin-left: auto;
    }
  }

  label {
    flex-grow: 1;
  }
}

.discount-code__title {
  font-family: var(--font-bold);
  font-size: 1.6rem;
  line-height: 2rem;
  @include bp(sm) {
    width: 100%;
  }
}

.delivery-radios {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(26rem, 1fr));
  gap: 3rem;
  padding-top: 1rem;
  border-top: 1px solid var(--color-primary-05);
  margin-top: 0.5rem;
}

.add-gift-card,
.gift-card-balance {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 2rem;
  padding-top: 1rem;
  border-top: 1px solid var(--color-primary-05);
  margin-top: 0.5rem;
}

.gift-card-balance {
  .select-wrapper {
    max-width: 29.5rem;
  }
}

.cart-total {
  display: flex;
  align-items: center;
  gap: 2rem;
  font-size: 1.6rem;
  @include bp(sm) {
    flex-wrap: wrap;
    justify-content: space-between;
    font-size: 1.4rem;
  }
}

.cart-total__title {
  margin-right: auto;
  @include bp(sm) {
    width: 100%;
  }
}

.cart-total__discount {
  color: var(--color-secondary);
}

.cart-total__savings {
  color: var(--color-green);
}

.cart-total__price {
  @include bp(sm) {
    margin-left: auto;
  }
}

.cart-form__controls {
  display: flex;
  justify-content: space-between;
  gap: 1rem;
  padding-top: 2rem;
  border-top: 1px solid var(--color-primary-05);
  margin-top: 1.5rem;
  @include bp-max(500px) {
    flex-wrap: wrap;
  }

  .button {
    width: 100%;
    max-width: 20rem;
    @include bp-max(500px) {
      max-width: initial;
    }
  }
}

/* Order confirmation page */

.security-panel {
  display: flex;
  justify-content: space-around;
  align-items: center;
  row-gap: 2rem;
  @include bp(sm) {
    flex-wrap: wrap;
  }
  @include bp-min(1040px) {
    flex-direction: column;
    align-items: initial;
  }

  hr {
    display: none;
    height: 0.1rem;
    border: none;
    background-color: var(--color-primary-05);
    @include bp-min(1040px) {
      display: block;
      flex-direction: column;
    }
  }
}

.security-panel-item {
  display: flex;
  justify-content: center;
  @include bp(sm) {
    flex-basis: calc(50% - 1rem);
  }
}

.security-panel-item__image {
  display: flex;
}

.order-confirmation-title {
  display: flex;
  align-items: center;
  gap: 1rem;
  margin-bottom: 1.4rem;
  font-family: var(--font-bold);
  font-size: 2rem;
  line-height: 2.5rem;
  &::before {
    content: '';
    width: 2.8rem;
    height: 2.8rem;
    border-radius: 50%;
    background-image: url('~SVG/ui/check.svg');
    background-position: center;
    background-repeat: no-repeat;
    background-size: 2rem;
    background-color: var(--color-primary);
  }
}

/* Order page */

.form-panel--order {
  legend {
    margin-bottom: 0;
  }
}

.table.order-table {
  th {
    padding-bottom: 1.5rem;
  }
  .cart-item-wrapper {
    padding-top: 1.5rem;
    @include bp-max(574.98px) {
      padding-top: 0;
    }
  }
  .cart-item__qty {
    padding-right: 2rem;
    text-align: center;
  }
}

.order-options {
  margin-top: 0.8rem;
}

.order-option {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 2rem;
  padding: 1.5rem 0;
  border-top: 1px solid var(--color-primary-05);
}

.order-option__checkbox {
  position: relative;
  .form__label {
    width: initial;
  }
}

.order-option__price {
  width: 13.4%;
  @include bp-max(574.98px) {
    width: initial;
  }
}

.order-total {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 2rem;
  padding-top: 1.5rem;
  border-top: 1px solid var(--color-primary-05);
  margin-top: -0.5rem;
  .button {
    width: 100%;
    max-width: 20rem;
  }
}

.order-total__price {
  display: flex;
  align-items: center;
  gap: 1rem;
  margin-right: 1.8rem;
  font-size: 1.6rem;
  @include bp-max(574.98px) {
    margin-right: 0;
  }
  .price {
    font-family: var(--font-bold);
    font-size: 1.8rem;
    line-height: 2.3rem;
  }
}

/* Payment information */

.payment-information {
  .hidden-field {
    display: none;
  }

  &__crypto-field {
    gap: 1.5rem;
  }
}

.crypto-methods {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
  @include bp-max(574.98px) {
    justify-content: center;
  }
}

.crypto-method {
  display: flex;
  width: 11.6rem;
  height: 9.6rem;
}

.crypto-radio {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 0.2rem;
  aspect-ratio: 116 / 96;
  border: 2px solid rgba(102, 102, 102, 0.2);
  border-radius: 1.4rem;
  transition: border-color var(--transition-medium-cubic);
  cursor: pointer;
  &:hover,
  &:focus-visible {
    border-color: rgba(102, 102, 102, 0.4);
  }

  .icon {
    font-size: 4rem;
  }
}

.crypto-radio__text {
  font-family: var(--font-bold);
  font-size: 1.2rem;
  line-height: 1.5rem;
}

.crypto-radio-input:checked + .crypto-radio {
  border-color: var(--color-secondary);
}

.warning-field {
  display: flex;
  align-items: center;
  gap: 1rem;
  min-height: 4.8rem;
  padding: 0.5rem 2rem;
  border-radius: 1rem;
  font-size: 1.2rem;
  line-height: 1.5rem;
  background-color: var(--color-secondary-10);
}

.warning-field__icon .icon {
  font-size: 1.8rem;
}

.warning-field__timer {
  font-family: var(--font-bold);
}

.crypto-transaction-info {
  display: grid;
  grid-template-columns: 14rem auto;
  gap: 1.6rem;
  @include bp(sm) {
    display: flex;
    flex-direction: column;
  }
  &__qr {
    grid-row: 1 / span 2;
    margin-top: 0.6rem;
    @include bp(sm) {
      order: 1;
      width: 14rem;
      margin: 0 auto;
    }
  }
  &__row--payment-id {
    grid-column: span 2;
    margin-top: -0.1rem;
  }
}

.crypto-transaction-info__label {
  margin-bottom: 0.5rem;
  font-size: 1.2rem;
  line-height: 1.5rem;
}

.crypto-transaction-info__field {
  position: relative;
  display: flex;
  align-items: center;
  gap: 1rem;
  width: 100%;
  min-height: 4.8rem;
  padding: 0.5rem 1.4rem 0.5rem 1.9rem;
  border: 1px solid var(--color-primary-20);
  border-radius: 1rem;

  .fiat-discount {
    color: var(--color-secondary);
  }
  .fiat-amount {
    margin-left: 0.1rem;
  }

  .copy-text {
    word-break: break-all;
  }

  .copy-button {
    margin-left: auto;
  }
}
