/*! Initial styling for basic html-elements */

@use '../abstracts' as *;

*,
::before,
::after {
  box-sizing: border-box;
  // outline: 1px solid lime;
}

* {
  padding: 0;
  margin: 0;
  &:focus {
    outline: none;
  }
  &:not(input, textarea):focus-visible {
    outline: 1px dotted var(--tab-focus-color);
  }
}

html {
  height: 100%;
  // Improve consistency of default fonts in all browsers
  font-family: system-ui, '-apple-system', 'Segoe UI', Roboto, Helvetica, Arial, sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji';
  @if $system-font-scale {
    // 1rem = 10px
    font-size: 62.5%;
  } @else {
    font-size: 10px;
  }
  background-color: var(--html-bg-color);
  scroll-behavior: smooth;
  // Prevent adjustments of font size after orientation changes in iOS
  -webkit-text-size-adjust: 100%;
}

body {
  overflow-x: hidden;
  height: 100%;
  font-family: var(--font-regular);
  font-size: 1.4rem;
  line-height: 2rem;
  color: var(--text-body-color);
  // MacOS browsers' font display
  -webkit-font-smoothing: antialiased;
  @include bp-max($container-min-width) {
    overflow-x: auto;
  }

  &.no-transition * {
    transition: none !important;
    &::before,
    &::after {
      transition: none !important;
    }
  }
}

p,
h1,
h2,
h3,
h4,
h5,
h6 {
  overflow-wrap: break-word;
}

h1,
.h1,
h2,
.h2,
h3,
.h3 {
  font-family: var(--font-bold);
  font-weight: 700;
}

h1,
.h1 {
  margin-bottom: 1rem;
  font-size: 3rem;
  line-height: 3.8rem;
  @include bp(md) {
    margin-bottom: 2rem;
    font-size: 4rem;
    line-height: 5rem;
  }
}

h2,
.h2 {
  margin-bottom: 1rem;
  font-size: 1.8rem;
  line-height: 2.2rem;
  @include bp(md) {
    font-size: 2.2rem;
    line-height: 2.8rem;
  }
}

h3,
.h3 {
  font-size: 1.6rem;
  line-height: 2rem;
}

p {
  margin-bottom: 1.6rem;
}

br.sm {
  @include bp(md) {
    display: none;
  }
}

br.md {
  @include bp(sm) {
    display: none;
  }
}

br.md-only {
  display: none;
  @include bp-only($bp-md-only) {
    display: initial;
  }
}

br.lg {
  display: none;
  @include bp(lg) {
    display: initial;
  }
}

ul,
ol {
  padding-left: 2rem;
  margin-bottom: 1.6rem;
  list-style-position: inside;
}

li {
  margin-bottom: 1rem;
  & > * {
    // Initial marker position on top
    vertical-align: top;
  }
}

a,
.link-button {
  display: inline-block;
  border: none;
  text-decoration: underline;
  background-color: transparent;
  color: var(--link-color);
  transition-property: transform, opacity, var(--transition-props-color);
  transition-timing-function: var(--transition-func-cubic);
  transition-duration: var(--transition-duration-medium);
  &:hover,
  &:focus-visible {
    text-decoration-color: transparent;
    color: var(--link-hover-color);
  }
  &:active {
    color: var(--link-active-color);
  }
}

.link-primary {
  text-decoration: none;
  color: var(--color-primary);
  &:hover,
  &:focus-visible {
    color: var(--color-secondary);
  }
  &:active {
    color: var(--color-accent);
  }
}

button,
input,
optgroup,
select,
textarea {
  // Change the font styles in all browsers
  font-family: inherit;
  font-size: 100%;
  // The same as the body line-height value, won't inherited
  line-height: 2rem;
}

input[type='radio'] {
  -webkit-appearance: none;
}

button,
select {
  // Remove the inheritance of text transform in Firefox
  text-transform: none;
}

select {
  // Prevent flickering while custom select lib is loading
  visibility: hidden;
}

button,
[type='button'],
[type='reset'],
[type='submit'] {
  // Correct the inability to style clickable types in iOS and Safari
  -webkit-appearance: button;
}

button,
[type='submit'] {
  display: block;
  &:hover {
    cursor: pointer;
  }
}

[type='search'] {
  // Correct the odd appearance in Chrome and Safari
  -webkit-appearance: textfield;
  // Correct the outline style in Safari
  outline-offset: -2px;
}

progress {
  // Add the correct vertical alignment in Chrome and Firefox
  vertical-align: baseline;
}

img,
picture,
video,
canvas,
svg {
  display: block;
  max-width: 100%;
}

img {
  box-sizing: content-box;
  width: 100%;
  height: auto;
}

.picture-svg img {
  // Correct initial appearance in Firefox
  width: revert-layer;
}

.inline-svg {
  // Disable title tooltip for inlined svg elements
  pointer-events: none;
}

nav ul {
  list-style: none;
}

nav li {
  display: inline-block;
}

address {
  font-style: normal;
}

table {
  // Remove text indentation from table contents in Chrome and Safari
  text-indent: 0;
  // Correct table border color inheritance in all Chrome and Safari
  border-color: inherit;
}

hr {
  // Add the correct height in Firefox
  height: 0;
  // Correct the inheritance of border color in Firefox
  color: inherit;
}

abbr[title] {
  // Add the correct text decoration in Chrome, Edge, and Safari
  text-decoration: underline dotted;
}

b,
strong {
  font-family: var(--font-bold);
  // Add the correct font weight in Edge and Safari
  font-weight: bolder;
}

code,
kbd,
samp,
pre {
  // Improve consistency of default fonts in all browsers
  font-family: ui-monospace, SFMono-Regular, Consolas, 'Liberation Mono', Menlo, monospace;
  // Correct the odd 'em' font sizing in all browsers
  font-size: 1em;
}

small {
  // Add the correct font size in all browsers
  font-size: 80%;
}

sub,
sup {
  position: relative;
  vertical-align: baseline;
  font-size: 75%;
  // Prevent 'sub' and 'sup' elements from affecting the line height in all browsers
  line-height: 0;
}

sub {
  bottom: -0.25em;
}

sup {
  top: -0.5em;
}

summary {
  // Add the correct display in Chrome and Safari
  display: list-item;
}

iframe {
  border: none;
}

::-moz-focus-inner {
  // Remove the inner border and padding in Firefox
  padding: 0;
  border-style: none;
}

:-moz-focusring {
  // Restore the focus styles unset by the previous rule
  outline: 1px dotted ButtonText;
}

// Remove the additional ':invalid' styles in Firefox
:-moz-ui-invalid {
  box-shadow: none;
}

::-webkit-inner-spin-button,
::-webkit-outer-spin-button {
  // Correct the cursor style of increment and decrement buttons in Safari
  height: auto;
}

::-webkit-search-decoration {
  // Remove the inner padding in Chrome and Safari on macOS
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  // Correct the inability to style clickable types in iOS and Safari
  -webkit-appearance: button;
  // Change font properties to 'inherit' in Safari
  font: inherit;
}
