/*! Accordion component */

@use '../abstracts/' as *;

.accordion-button {
  position: relative;
  cursor: pointer;
  &::after {
    content: '';
    position: absolute;
    top: 2.2rem;
    right: 0;
    width: 2.4rem;
    height: 2.4rem;
    transition: all 0.2s ease-in-out;
  }
}

.accordion-button[aria-expanded='true']::after {
  transform: rotate(180deg);
}

.accordion-panel {
  display: grid;
  grid-template-rows: 0fr;
  transition: grid-template-rows 0.2s ease-out;
}

.accordion-button[aria-expanded='true'] + .accordion-panel {
  grid-template-rows: 1fr;
}

.accordion-content {
  overflow: hidden;
}

.accordion--mobile {
  @include bp(md) {
    .accordion-button {
      cursor: default;
      pointer-events: none;
    }
    .accordion-panel {
      grid-template-rows: 1fr;
    }
  }
}
