/*! Non-major inner pages */

@use '../abstracts/' as *;

/* Raw content articles */

.raw-content {
  @include bp(md) {
    font-size: 1.6rem;
    line-height: 2.4rem;
  }

  p {
    @include bp(md) {
      margin-bottom: 2.4rem;
    }
    &:last-child {
      margin-bottom: 0;
    }
    & + h2 {
      margin-top: 2rem;
      @include bp(md) {
        margin-top: 3rem;
      }
    }
    & + ul {
      margin-top: -1.6rem;
      @include bp(md) {
        margin-top: -2.4rem;
      }
    }
  }

  ul {
    padding-left: 0.8rem;
    list-style: none;
    @include bp(md) {
      margin-bottom: 2.4rem;
    }
  }

  li {
    display: flex;
    margin-bottom: 0;
    &::before {
      content: '';
      display: inline-block;
      margin-right: 0.8rem;
    }
  }

  hr {
    display: block;
    height: 1px;
    border: none;
    margin-bottom: 1.9rem;
    background-color: var(--color-primary-10);
  }

  &__section-mt {
    margin-top: 2rem;
    @include bp(md) {
      margin-top: 3rem;
    }
  }

  &__lists {
    ul {
      @include bp(md) {
        margin-bottom: 1.2rem;
      }
    }
    & + h2 {
      margin-top: 2rem;
      @include bp(md) {
        margin-top: 3rem;
      }
    }
  }
}

.raw-content--narrow {
  p {
    @include bp(md) {
      margin-bottom: 2rem;
    }
    & + ul {
      @include bp(md) {
        margin-top: -2rem;
      }
    }
  }

  .default-paragraph {
    @include bp(md) {
      margin-bottom: 2.4rem;
    }
  }
}

.raw-content--small {
  h2 {
    font-size: 2rem;
    line-height: 2.5rem;
  }
  p {
    font-size: 1.4rem;
    line-height: 2rem;
  }
}

.raw-content--help {
  h2 {
    margin-bottom: 0.5rem;
  }
}

.raw-content.raw-content--complete {
  p {
    margin-bottom: 1.6rem;
    &.small-text {
      margin: -0.4rem 0 0;
      font-size: 1.4rem;
      line-height: 2.2rem;
    }
  }
}

/* Testimonials */

.testimonials {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(26rem, 1fr));
  gap: 1rem;
  @include bp(md) {
    gap: 2rem;
  }
}

.show-more {
  display: flex;
  justify-content: center;
  margin-top: 2rem;
}

/* Gift card page */

.gift-wrapper {
  display: flex;
  gap: 1rem;

  .button {
    flex-shrink: 0;
    @include bp(sm) {
      .button__text {
        display: none;
      }
    }
  }
}
