/*! Homepage */

@use '../abstracts/' as *;

.product-cards {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(17.5rem, 1fr));
  gap: 1rem;
  @include bp-max(359.98px) {
    display: flex;
    flex-direction: column;
  }
  @include bp(xs) {
    grid-template-columns: 1fr 1fr;
  }
  @include bp(md) {
    grid-template-columns: repeat(auto-fit, minmax(28rem, 1fr));
    gap: 2rem;
  }
}
